import React, { useCallback, useEffect, useRef } from "react";
import vid from "../../assets/icons/vid.MP4";
import backTemp from "../../assets/icons/closeTemp.svg";
import fireWhite from "../../assets/icons/fireWhite.png";
import revert from "../../assets/icons/revert.svg";
import trash from "../../assets/icons/trashT.svg";
import share from "../../assets/icons/shareTemp.png";
import { Link, useSearchParams } from "react-router-dom";
import timeine from "../../assets/icons/timeLine.svg";
import { mockTEmplates } from "./mokTemplates";
import more from "../../assets/icons/moreTemp.png";
import backArrow from "../../assets/icons/bactArrow.png";
const ShowTemplate = ({ windowWidth, setShow, show, id }) => {
  let [searchParams, setSearchParams] = useSearchParams();
  const cof = windowWidth / 393;

  const shareF = useCallback(async (url) => {
    console.log(url);
    try {
      const response = await fetch(
        `https://stingray-app-lodnw.ondigitalocean.app/${mockTEmplates[id].link}`
      );
      const blob = await response.blob();
      const file = new File([blob], "share.MP4", { type: "video/mp4" });
      console.log(file);
      await navigator.share({
        files: [file],
        // title: "Awesome Picture",
        // text: "Check out this awesome picture!",
      });

      console.log("Sharing was successful.");
    } catch (err) {
      console.log("Error during sharing", err);
    }
  }, []);
  const videoRef = useRef(null);
  useEffect(() => {
    const playVideo = () => {
      if (videoRef.current) {
        videoRef.current.play();
      }
    };

    playVideo();
  }, []);
  return (
    <div
      style={{
        width: "100%",
        height: "100vh",
        background: "#ddc2a2",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        position: "absolute",
        zIndex: 99999,
        top: 0,
        flexDirection: "column",
      }}
    >
      {/* <div
        style={{
          width: 371 * cof,
          height: "90%",
          background: "rgba(0, 0, 0, 0.25)",
          borderRadius: 10,
        }}
      ></div> */}
      <div
        style={{
          width: 361 * cof,
          height: 70 * cof,
          background: "#2D2D2D",
          borderTopRightRadius: 20,
          borderTopLeftRadius: 20,
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <div
          style={{
            width: 320 * cof,
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
          }}
        >
          <img
            alt="back"
            src={backArrow}
            style={{ width: 10 * cof }}
            onClick={() => setShow(false)}
          />
          <img
            alt="share"
            src={share}
            style={{ width: 27 * cof }}
            onClick={() => shareF()}
          />
          <img alt="more" src={more} style={{ width: 21 * cof }} />
        </div>
      </div>
      <div
        style={{
          overflow: "hidden",
          width: "auto",
          height: "auto",
          position: "relative",
          borderBottomRightRadius: 20,
          borderBottomLeftRadius: 20,
        }}
      >
        <video
          style={{
            width: 361 * cof,
            // height: 600,
          }}
          autoPlay
          // muted
          loop={true}
          playsInline={true}
          controls
          ref={videoRef}
        >
          <source src={mockTEmplates[id].video} type="video/mp4" />
        </video>
      </div>
    </div>
  );
};

export default ShowTemplate;
