import React from "react";
import activeArrow from "../../assets/icons/activeArrow.png";
import inactive from "../../assets/icons/inactiveArrow.png";

const DeliveryInfo = ({ cof, data, setData }) => {
  return (
    <div style={{ marginTop: 40 * cof, marginLeft: 17 * cof }}>
      {data.map((item, key) => (
        <div key={key}>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              width: 120 * cof,
              justifyContent: "space-between",
              marginTop: 17 * cof,
            }}
            onClick={() => setData(key)}
          >
            <div style={{ fontSize: 11 * cof }}>{item.title}</div>
            {item.title !== "Credit card info" && (
              <img
                alt="img"
                src={item.active ? activeArrow : inactive}
                style={{ height: 5 * cof }}
              />
            )}
          </div>
          {item.active && <div>{item.block}</div>}
        </div>
      ))}
    </div>
  );
};

export default DeliveryInfo;
