import React from "react";
import active from "../../assets/icons/activeCheckBox.png";
import inactive from "../../assets/icons/inactiveCheckbox.png";

const CheckBox = ({ data, setData, cof }) => {
  return (
    <div style={{ marginLeft: 17 * cof, marginTop: 8 * cof }}>
      {data.map((item, key) => (
        <div
          key={key}
          style={{
            display: "flex",
            width: 130 * cof,
            justifyContent: "space-between",
            alignItems: "center",
            marginTop: 7 * cof,
          }}
          onClick={() => setData(key)}
        >
          <div>{item.type}</div>
          <img
            alt="checkbox"
            src={item.active ? active : inactive}
            style={{ width: 23 * cof, height: 23 * cof }}
          />
        </div>
      ))}
    </div>
  );
};

export default CheckBox;
