import React, { useState } from "react";
import Description from "./Description";
import Color from "./Color";
import Review from "./Review";

const Constructor = ({
  cof,
  setSize,
  menu,
  changeMenu,
  sizes,
  colors,
  changeColor,
  pwa,
}) => {
  return (
    <div>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          marginTop: 14 * cof,
          marginLeft: 17 * cof,
        }}
      >
        <div>SIZE</div>
        <div style={{ display: "flex" }}>
          {sizes.map((item, i) => (
            <div
              style={{
                width: pwa ? 41 * cof : 29 * cof,
                height: pwa ? 41 * cof : 29 * cof,
                borderRadius: 5 * cof,
                fontSize: 10 * cof,
                color: "#000000",
                boxShadow:
                  "0px 4px 4px 0px rgba(0, 0, 0, 0.05) inset, 0px 4px 4px 0px rgba(0, 0, 0, 0.05)",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                marginLeft: 5 * cof,
                marginLeft: 14 * cof,
                backgroundColor: item.active ? "#D4F2B5" : "#fff",
              }}
              key={i}
              onClick={() => setSize(i)}
            >
              {item.title}
            </div>
          ))}
        </div>
      </div>
      <div
        style={{
          display: "flex",
          width: 359 * cof,
          marginLeft: 17 * cof,
          justifyContent: "space-between",
        }}
      >
        {menu.map((item, key) => (
          <div
            key={key}
            style={{
              fontSize: 14 * cof,
              color: item.active ? "#D4F2B5" : "#000",
              fontWeight: 500,
              marginTop: 15 * cof,
            }}
            onClick={() => changeMenu(key)}
          >
            {item.title}
          </div>
        ))}
      </div>
      <div
        style={{
          width: 359 * cof,
          marginLeft: 17 * cof,
          marginTop: 20 * cof,
          position: "relative",
        }}
      >
        <div
          style={{
            width: 359 * cof,
            height: 2,
            backgroundColor: "#CBF1A4",
          }}
        ></div>
        <div
          style={{
            display: "flex",
            position: "absolute",
            top: -5.5,
            width: 359 * cof,
            justifyContent: "space-between",
            // left: 30,
          }}
        >
          {menu.map((itm, key) => (
            <div
              style={{
                width: 110 * cof,
                height: 3 * cof,
                backgroundColor: "#CBF1A4",
                borderRadius: 100,
                opacity: itm.active ? 1 : 0,
                marginTop: 3,
              }}
              key={key}
            ></div>
          ))}
        </div>
      </div>
      {menu[0].active && (
        <div style={{ marginTop: 15 }}>
          <Description cof={cof} />
        </div>
      )}
      {menu[1].active && (
        <div>
          <Color color={colors} cof={cof} changeColor={changeColor} />
        </div>
      )}
      {menu[2].active && (
        <div>
          <Review cof={cof} />
        </div>
      )}
    </div>
  );
};

export default Constructor;
