import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  setDoneStep1,
  setDoneStep2,
  setSelectTExt,
} from "../../redux/appReducer";
import { texts } from "../../utils/mockTexts";

const AnimatedText = ({ text, cof, setDone, step, setUserText }) => {
  const [displayText, setDisplayText] = useState("");
  const { indexText } = useSelector((state) => state.appReducer);
  const dispatch = useDispatch();
  const copy = () => {
    navigator.clipboard
      .writeText(texts[indexText].text)
      .then(() => {
        console.log("copy");
      })
      .catch((err) => console.log(" : ", err));
  };
  useEffect(() => {
    let currentText = "";
    let currentIndex = 0;
    const interval = setInterval(() => {
      if (currentIndex < text.length) {
        currentText += text[currentIndex];
        setDisplayText(currentText);
        currentIndex++;
      } else {
        console.log(texts[indexText].text);
        copy();
        if (step === "step1") {
          dispatch(setDoneStep1(true));
        } else {
          dispatch(setDoneStep2(true));
        }

        console.log("done");
        clearInterval(interval);
      }
    }, 50); // ,   

    return () => clearInterval(interval); //     
  }, [text]);

  return (
    <div
      style={{
        fontSize: 14 * cof,
        fontWeight: 700,
        color: "#4E463C",
        lineHeight: "normal",
        letterSpacing: "0.368px",
        width: 367 * cof,
        marginLeft: 10 * cof,
        marginTop: 15 * cof,
      }}
      onClick={() => setUserText(displayText)}
    >
      {displayText}
    </div>
  );
};

export default AnimatedText;
