import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { setMenuShow } from "../../redux/appReducer";
import albomCart from "../../assets/icons/albumCart.png";

const BottomBar = ({ windowWidth, indexType, length, album }) => {
  const { pwa } = useSelector((state) => state.appReducer);
  const cof = windowWidth / 393;
  const navigation = useNavigate();
  const [data, setData] = useState([]);

  useEffect(() => {
    const cart = localStorage.getItem("cartData");
    const cartArray = JSON.parse(cart);
    if (length) {
      setData(length);
    } else {
      if (cartArray) {
        setData(JSON.parse(cart));
      }
    }
  }, [setData, length]);

  const [menu, setMenu] = useState([
    {
      img:
        indexType === 0
          ? require("../../assets/icons/homeBlack.png")
          : require("../../assets/icons/home.png"),
      title: "Home",
      link: "/",
      id: "home",
      active: false,
    },
    {
      img:
        indexType === 1
          ? require("../../assets/icons/albumBlack.png")
          : require("../../assets/icons/album.png"),
      title: "Album",
      link: "/buy_img",
      id: "album",
      active: false,
    },
    {
      img:
        indexType === 2
          ? require("../../assets/icons/storeBlack.png")
          : require("../../assets/icons/store.png"),
      title: "Store",
      link: "/store",
      active: false,
    },
    {
      img:
        indexType === 3
          ? require("../../assets/icons/cartBlack.png")
          : require("../../assets/icons/cart.png"),
      title: "Cart",
      link: "/cart",
      active: false,
    },
    {
      img:
        indexType === 4
          ? require("../../assets/icons/menuBlack.png")
          : require("../../assets/icons/menu.png"),
      title: "Menu",
      link: "/menu",
      active: false,
    },
    {
      img:
        indexType === 5
          ? require("../../assets/icons/clubBlack.png")
          : require("../../assets/icons/club.png"),
      title: "Club",
      link: "/club",
      active: false,
    },
  ]);
  const navigate = (link) => {
    navigation(link);
  };

  useEffect(() => {
    let newArray = [...menu];
    newArray[indexType].active = true;
    setMenu(newArray);
  }, [setMenu]);
  return (
    <div
      style={{
        width: 393 * cof,
        height: pwa ? 91 * cof : 65 * cof,
        position: "fixed",
        bottom: 0,
        backgroundColor: "#fff",
        boxShadow: "4px 2px 100px 100px rgba(0, 0, 0, 0.05)",
        zIndex: 999,
      }}
    >
      <div
        style={{
          display: "flex",
          width: 329 * cof,
          marginLeft: 32 * cof,
          justifyContent: "space-between",
          marginTop: 10,
        }}
      >
        {menu.map((item, key) => (
          <div
            key={key}
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              position: "relative",
            }}
            onClick={() => navigate(item.link)}
          >
            {item.title === "Cart" && data.length !== 0 && album && (
              <div
                style={{
                  position: "absolute",
                  zIndex: 999,
                  bottom: pwa ? 50 * cof : 52 * cof,
                  right: -120 * cof,
                }}
              >
                <img
                  alt="cart"
                  src={albomCart}
                  style={{
                    width: 335 * cof,
                    height: 68 * cof,
                  }}
                />
                {length && (
                  <div
                    style={{
                      position: "absolute",
                      top: 18 * cof,
                      left: 100 * cof,
                      fontSize: 15 * cof,
                      fontWeight: 500,
                      color: "#fff",
                    }}
                  >
                    View Cart  ${length.length * 20}.00
                  </div>
                )}
              </div>
            )}
            {item.title === "Cart" && data.length !== 0 && (
              <div
                style={{
                  width: 21 * cof,
                  height: 21 * cof,
                  backgroundColor: "#14A38B",
                  borderRadius: 100,
                  position: "absolute",
                  right: -10 * cof,
                  top: -10 * cof,
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  color: "#fff",
                }}
              >
                {data.length}
              </div>
            )}
            <img src={item.img} style={{ height: 27 * cof }} />
            <div
              style={{
                fontSize: 8 * cof,
                marginTop: 5,
                fontWeight: "900",
                color: item.active ? "#000" : "#888687",
              }}
            >
              {item.title}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default BottomBar;
