import React from "react";
import logo from "../../assets/icons/logo.png";
import menu from "../../assets/icons/menu.svg";
import { useDispatch, useSelector } from "react-redux";
import { setMenuShow } from "../../redux/appReducer";

const Header = ({ windowWidth }) => {
  const { pwa } = useSelector((state) => state.appReducer);
  const cof = windowWidth / 393;
  const dispatch = useDispatch();

  return (
    <div
      style={{
        display: "flex",
        width: 362 * cof,
        paddingTop: pwa ? 29 * cof : 12 * cof,
        marginLeft: 11 * cof,
        justifyContent: "space-between",
        background: "#fcfcfc",
      }}
    >
      <div>
        <img src={logo} style={{ width: 160 * cof }} alt="logo" />
      </div>
      <div
        style={{
          width: 50 * cof,
          height: 50 * cof,
          background: "#fff",
          borderRadius: 11 * cof,
          boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.10) inset",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          marginTop: 17 * cof,
        }}
        onClick={() => dispatch(setMenuShow(true))}
      >
        <img
          alt="menu"
          src={menu}
          style={{ width: 34 * cof, height: 34 * cof }}
        />
      </div>
    </div>
  );
};

export default Header;
