import React from "react";
import logo from "../../assets/icons/logoBlack.png";
import close from "../../assets/icons/close.png";
import right from "../../assets/icons/arrowRight.svg";
import trash from "../../assets/icons/trashSet.svg";
import { useDispatch, useSelector } from "react-redux";
import { setMenuShow, setSettings } from "../../redux/appReducer";
import trashMenu from "../../assets/icons/trashMenu.svg";
import notifMenu from "../../assets/icons/notifMenu.svg";
import creatorMenu from "../../assets/icons/creatorMenu.svg";
import accauntmenu from "../../assets/icons/accauntMenu.svg";
import archvemenu from "../../assets/icons/archiveMenu.svg";
import orderMenu from "../../assets/icons/orderMenu.svg";
import helpMenu from "../../assets/icons/helpMenu.svg";
import login from "../../assets/icons/login.svg";
import logout from "../../assets/icons/logOut.svg";

const SettinpsPopup = ({ windowWidth }) => {
  const dispatch = useDispatch();
  const { pwa } = useSelector((state) => state.appReducer);
  const cof = windowWidth / 393;
  const list = [
    {
      title: "Delete this page",
      img: trashMenu,
    },
    {
      title: "Notifications",
      img: notifMenu,
    },
    {
      title: "Creators tools",
      img: creatorMenu,
    },
    {
      title: "Account privacy",
      img: accauntmenu,
    },
    {
      title: "Archiving and downloading",
      img: archvemenu,
    },
    {
      title: "Orders and payments",
      img: orderMenu,
    },
    {
      title: "Help",
      img: helpMenu,
    },
    {
      title: "Login",
      img: login,
    },
    {
      title: "Log out",
      img: logout,
    },
  ];
  const changeMenu = () => {
    dispatch(setMenuShow(true));
    dispatch(setSettings(false));
  };
  return (
    <div
      style={{
        width: "100%",
        height: "100vh",
        position: "absolute",
        background: "#fff",
        top: 0,
        left: 0,
        zIndex: 9999,
      }}
    >
      <div
        style={{
          display: "flex",
          width: 340 * cof,
          alignItems: "center",
          justifyContent: "space-between",
          marginLeft: 29 * cof,
          marginTop: 20,
        }}
        onClick={() => changeMenu()}
      >
        <div style={{ fontSize: 16 * cof, fontWeight: "300" }}>Settings</div>
        <img alt="close" src={right} width={23 * cof} />
      </div>
      <img
        alt="logo"
        src={logo}
        width={77 * cof}
        height={51 * cof}
        style={{ marginLeft: 284 * cof, marginTop: 46 * cof }}
      />

      <div style={{ marginTop: 65 * cof }}>
        {list.map((item, key) => (
          <div
            style={{
              display: "flex",
              alignItems: "center",
              marginLeft: 30 * cof,
              marginTop: pwa ? 33 * cof : 23 * cof,
            }}
            key={key}
          >
            <img src={item.img} alt="menu" width={19 * cof} height={19 * cof} />
            <div
              style={{
                fontSize: 18 * cof,

                marginLeft: 14 * cof,
                fontWeight: 300,
              }}
            >
              {item.title}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default SettinpsPopup;
