import React, { useEffect, useState } from "react";
import ReviewHeader from "./ReviewHeader";
import startBack from "../../assets/icons/backgroundStep3.png";
import logoTiki from "../../assets/icons/tikiLogoReview.png";
import imgRview from "../../assets/icons/imgReview.png";
import waves from "../../assets/icons/waves.png";
import ScrollLoop from "../Store/scrollLoop";
import Messages from "./Messages";
import RangeSlider from "../Store/RangeSlider";
import Range from "./Range";
import TextCont from "./TextCont";
import BtnCont from "./BtnCont";
import regenerate from "../../assets/icons/regenerate.png";
import logoRegenerate from "../../assets/icons/logoRegenerate.png";
import { useDispatch, useSelector } from "react-redux";
import {
  setActiveStep2,
  setDoneStep2,
  setIndexText,
  setPrevText,
  setReviewImages,
  setSelectTExt,
  setSlider4,
  setSlider5,
  setSlider6,
} from "../../redux/appReducer";
import { texts } from "../../utils/mockTexts";
import { useNavigate } from "react-router-dom";
let slider_4 = [0];
let slider_5 = [0];
let slider_6 = [0];
const ReviewStep3 = ({ windowWidth }) => {
  const [allItems, setAllItems] = useState([
    {
      item: "Share your experience with our staff!...",
    },
    { item: "Just one more question before I start writing..." },
    {
      item: "Thank you so much for giving us a high rating!",
    },
    {
      item: "If you're satisfied with the result, press 'Rate Now' to visit our Yelp page...",
    },
  ]);
  const [items, setItems] = useState([
    {
      item: "Use all 3 fire emoji sliders for your updated review to magically appear...",
    },
  ]);
  useEffect(() => {
    slider_4 = [0];
    slider_5 = [0];
    slider_6 = [0];
  }, []);
  const cof = windowWidth / 393;
  const {
    slider4,
    slider5,
    slider6,
    doneStep2,
    selectText,
    indexText,
    reviewImages,
    slider1,
    slider2,
    slider3,
  } = useSelector((state) => state.appReducer);
  const [activeStep1, setActiveStep1] = useState(false);
  const [userText, setUserText] = useState(false);
  const navigate = useNavigate();

  const dispatch = useDispatch();
  const changeSlide4 = (e) => {
    slider_4 = e;
    dispatch(setSlider4(e));
    changeActive();
    let newArray = [...reviewImages];
    newArray = [...newArray, { img: require("../../assets/icons/eat.png") }];
    dispatch(setReviewImages(newArray));
  };
  const changeSlide5 = (e) => {
    slider_5 = e;
    dispatch(setSlider5(e));
    changeActive();
    let newArray = [...reviewImages];
    newArray = [...newArray, { img: require("../../assets/icons/server.png") }];
    dispatch(setReviewImages(newArray));
  };
  const changeSlider6 = (e) => {
    slider_6 = e;
    dispatch(setSlider6(e));
    changeActive();
  };
  const changeActive = () => {
    if (slider_4[0] !== 0 && slider_5[0] !== 0 && slider_6[0] !== 0) {
      if (
        Number(
          (
            (slider1[0] +
              slider2[0] +
              slider3[0] +
              slider_4[0] +
              slider_5[0] +
              slider_6[0]) /
            20 /
            6
          ).toFixed(0)
        ) < 4
      ) {
        navigate("/alt_review");
      } else {
        dispatch(setIndexText(3));
        dispatch(setActiveStep2(true));
      }
    }
  };

  const timeout = () => {
    if (doneStep2) {
      setActiveStep1(false);
      dispatch(setDoneStep2(false));
      dispatch(setSelectTExt(false));
      dispatch(
        setIndexText(indexText === 5 || indexText < 3 ? 3 : indexText + 1)
      );
      setPrevText(selectText);
      if (indexText > 3) {
        dispatch(setPrevText({ index: indexText }));
      }
      setTimeout(() => changeIndex(), 100);
    }
  };
  const changeIndex = () => {
    setActiveStep1(true);
  };

  return (
    <div style={{ overflow: "hidden" }}>
      <ReviewHeader cof={cof} back={"/review_step2"} />

      <div style={{ top: 69 * cof, position: "absolute" }}>
        <Messages
          allItems={allItems}
          setAllItems={setAllItems}
          items={items}
          setItems={setItems}
          cof={cof}
        />
        {/* <Messages /> */}
        <Range
          cof={cof}
          slider1={slider4}
          slider2={slider5}
          slider3={slider6}
          setSlider1={changeSlide4}
          setSlider2={changeSlide5}
          setSlider3={changeSlider6}
          q1={"How much did you enjoy our Ahi Tuna Tataki?"}
          q2={"Did Leilani, your server, do a good job during your visit?"}
          q3={
            "Considering your experience, would you be inclined to recommend us to your friends and family?"
          }
        />
        <div
          style={{
            width: 335 * cof,
            height: 1,
            background: "#ECCBA3",
            position: "absolute",
            top: 174 * cof,
            left: 26.5 * cof,
          }}
        ></div>
        <div
          style={{ position: "absolute", right: 20 * cof, top: -20 * cof }}
          onClick={timeout}
        >
          <img
            src={logoTiki}
            alt="logo"
            style={{
              width: 104 * cof,
              // height: 174 * cof,
            }}
          />
          {doneStep2 && (
            <img
              src={regenerate}
              alt="regenerate"
              style={{
                width: 100 * cof,
                position: "absolute",
                left: 20 * cof,
                top: 80 * cof,
              }}
            />
          )}
          {doneStep2 && (
            <img
              src={logoRegenerate}
              alt="regenerate"
              style={{
                width: 42 * cof,
                position: "absolute",
                left: -30 * cof,
                top: 60 * cof,
              }}
            />
          )}
        </div>

        <TextCont
          cof={cof}
          active={activeStep1}
          indexText={indexText}
          step={"step2"}
          userText={userText}
          setUserText={setUserText}
        />
        <div style={{ position: "absolute", top: 445 * cof }}>
          <img
            src={waves}
            alt="waves"
            style={{ width: 393 * cof, position: "absolute" }}
          />
          <BtnCont cof={cof} userText={userText} />
          <div
            style={{
              top: 146 * cof,
              width: 393 * cof,
              position: "absolute",
            }}
          >
            <ScrollLoop>
              {reviewImages.map((item, key) => (
                <img
                  src={item.img}
                  style={{ height: 133 * cof, margin: 5 * cof }}
                  key={key}
                />
              ))}
            </ScrollLoop>
          </div>
        </div>

        <img src={startBack} style={{ width: 393 * cof }} />
      </div>
    </div>
  );
};

export default ReviewStep3;
