import React, { useEffect, useState } from "react";
import BottomBar from "./Components/BottomBar/BottomBar";
import Header from "./Components/Header/Header";
import AlbumPage from "./Components/AlbumPage/AlbumPage";
import {
  BrowserRouter,
  Route,
  Routes,
  useSearchParams,
} from "react-router-dom";
import Store from "./Components/Store/Store";
import Tshirt from "./Components/Store/Tshirt";
import Cart from "./Components/Cart/Cart";
import PageBuyImg from "./Components/PageBuyImg/PageBuyImg";
import Product from "./Components/Product/Product";
import Menu from "./Components/Menu/Menu";
import { useDispatch, useSelector } from "react-redux";
import RestMenu from "./Components/RestMenu/RestMenu";
import { setPwa } from "./redux/appReducer";
import Pay1 from "./Components/Pay/Pay1";
import PayInfo from "./Components/Pay/PayInfo";
import Order from "./Components/Order/Order";
import Club from "./Components/Club/Club";
import SettinpsPopup from "./Components/SettingsPopup/SettinpsPopup";
import Templates from "./Components/Templates/Templates";
import CreateTemplate from "./Components/Templates/CreateTemplate";
import ShowTemplate from "./Components/Templates/ShowTemplate";
import Archive from "./Components/Archive/Archive";
import Review from "./Components/Review/Review";
import ReviewStep2 from "./Components/Review/ReviewStep2";
import ReviewStep3 from "./Components/Review/ReviewStep3";
import Done from "./Components/Review/Done";
import AltReview from "./Components/Review/AltReview";
import LiveMenu from "./Components/LiveMenu/LiveMenu";
import LiveMenuItem from "./Components/LiveMenu/LiveMenuItem";
import LiveMenuShowItem from "./Components/LiveMenu/LiveMenuShowItem";
import OrderMenu from "./Components/LiveMenu/Order";
import CheckoutMenu from "./Components/LiveMenu/CheckoutMenu";

const App = () => {
  const totalTime = 900;
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const { menu, settings } = useSelector((state) => state.appReducer);
  const [timeRemaining, setTimeRemaining] = useState(900); // 15 minutes in seconds
  const [progress, setProgress] = useState(100);

  useEffect(() => {
    const interval = setInterval(() => {
      if (timeRemaining > 0) {
        const newTimeRemaining = timeRemaining - 1;
        setTimeRemaining(newTimeRemaining);
        setProgress(((totalTime - newTimeRemaining) / totalTime) * 100); // Calculate progress in percentage
      }
    }, 1000); // Update every 1 second

    return () => clearInterval(interval); // Clean up the interval on unmount
  }, [timeRemaining]);
  const formatTime = (seconds) => {
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    return `${minutes}:${remainingSeconds < 10 ? "0" : ""}${remainingSeconds}`;
  };
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(
      setPwa(
        window.navigator.standalone ||
          (window.matchMedia &&
            window.matchMedia("(display-mode: standalone)").matches)
      )
    );
  }, [setPwa]);

  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);
  useEffect(() => {
    const first = localStorage.getItem("first");
    if (!first) {
      localStorage.clear();
      localStorage.setItem("first", "true");
    }
  });
  return (
    <div style={{ backgroundColor: "#FCFCFC" }}>
      <BrowserRouter>
        <div>
          {menu && <Menu windowWidth={windowWidth} />}
          {settings && <SettinpsPopup windowWidth={windowWidth} />}

          <Routes>
            <Route path="/" element={<AlbumPage windowWidth={windowWidth} />} />
            <Route
              path="/album"
              element={<PageBuyImg windowWidth={windowWidth} />}
            />
            <Route
              path="/store"
              element={<Store windowWidth={windowWidth} />}
            />
            <Route
              path="/t_shirt"
              element={<Tshirt windowWidth={windowWidth} />}
            />
            <Route
              path="/buy_img"
              element={<PageBuyImg windowWidth={windowWidth} />}
            />
            <Route path="/cart" element={<Cart windowWidth={windowWidth} />} />
            <Route
              path="/product"
              element={<Product windowWidth={windowWidth} />}
            />
            <Route
              path="/menu"
              element={<RestMenu windowWidth={windowWidth} />}
            />
            <Route
              path="/pay_data"
              element={<Pay1 windowWidth={windowWidth} />}
            />
            <Route
              path="/pay_info"
              element={<PayInfo windowWidth={windowWidth} />}
            />
            <Route
              path="/order"
              element={<Order windowWidth={windowWidth} />}
            />
            <Route path="/club" element={<Club windowWidth={windowWidth} />} />
            <Route
              path="/templates"
              element={<Templates windowWidth={windowWidth} />}
            />
            <Route
              path="/template"
              element={<CreateTemplate windowWidth={windowWidth} />}
            />
            <Route
              path="/show_template"
              element={<ShowTemplate windowWidth={windowWidth} />}
            />
            <Route
              path="/archive"
              element={<Archive windowWidth={windowWidth} />}
            />
            <Route
              path="/review"
              element={<Review windowWidth={windowWidth} />}
            />
            <Route
              path="/review_step2"
              element={<ReviewStep2 windowWidth={windowWidth} />}
            />
            <Route
              path="/review_step3"
              element={<ReviewStep3 windowWidth={windowWidth} />}
            />
            <Route path="/done" element={<Done windowWidth={windowWidth} />} />
            <Route
              path="/alt_review"
              element={<AltReview windowWidth={windowWidth} />}
            />
            <Route
              path="/live_menu"
              element={
                <LiveMenu
                  windowWidth={windowWidth}
                  timeRemaining={timeRemaining}
                  formatTime={formatTime}
                  progress={progress}
                />
              }
            />
            <Route
              path="/live_menu_item"
              element={
                <LiveMenuItem
                  windowWidth={windowWidth}
                  timeRemaining={timeRemaining}
                  formatTime={formatTime}
                  progress={progress}
                />
              }
            />
            <Route
              path="/live_menu_show_item"
              element={
                <LiveMenuShowItem
                  windowWidth={windowWidth}
                  timeRemaining={timeRemaining}
                  formatTime={formatTime}
                  progress={progress}
                />
              }
            />
            <Route
              path="/menu_order"
              element={
                <OrderMenu
                  windowWidth={windowWidth}
                  timeRemaining={timeRemaining}
                  formatTime={formatTime}
                  progress={progress}
                />
              }
            />
            <Route
              path="/checkout_menu"
              element={
                <CheckoutMenu
                  windowWidth={windowWidth}
                  timeRemaining={timeRemaining}
                  formatTime={formatTime}
                  progress={progress}
                />
              }
            />
          </Routes>
        </div>
      </BrowserRouter>
    </div>
  );
};

export default App;
