import React from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Carousel from "nuka-carousel";

function SliderS({ components }) {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    swipe: true,
    arrows: false,
    customPaging: (i) => (
      <div
        style={{
          width: "10px",
          color: "blue",
          borderRadius: "50%",
        }}
      >
        {i + 1}
      </div>
    ),
  };

  return (
    <Carousel
      {...settings}
      withoutControls={true}
      autoplay={true}
      wrapAround={true}
      autoplayInterval={7000}
    >
      {components.map((item, key) => (
        <div key={key}>{item}</div>
      ))}
    </Carousel>
  );
}

export default SliderS;
