import React from "react";
import back from "../../assets/icons/back.png";
import shareMenu from "../../assets/icons/shareMenu.png";
import { Link, useSearchParams } from "react-router-dom";
import { useSelector } from "react-redux";

const HeaderProduct = ({ cof }) => {
  let [searchParams, setSearchParams] = useSearchParams();
  const { pwa } = useSelector((state) => state.appReducer);
  const shareLink = () => {
    const url = window.location.href;
    console.log(url);
    if (navigator.share) {
      //   API share  
      navigator
        .share({
          url: url,
        })
        .then(() => console.log("  "))
        .catch((error) =>
          console.error("    :", error)
        );
    } else {
      console.log("API share     ");
    }
  };
  return (
    <div style={{ width: "100%" }}>
      <div
        style={{
          display: "flex",
          width: 359 * cof,
          marginLeft: 17 * cof,
          justifyContent: "space-between",
          alignItems: "center",
          marginTop: 29 * cof,
        }}
      >
        <Link
          style={{
            width: 50 * cof,
            height: 50 * cof,
            borderRadius: 10,
            boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.10) inset",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
          to={`/${searchParams.get("back")}?category=${searchParams.get(
            "category"
          )}`}
        >
          <img
            alt="back"
            src={back}
            style={{ width: 34 * cof, height: 34 * cof }}
          />
        </Link>
        <div style={{ fontSize: 14 * cof, color: "#000", fontWeight: 800 }}>
          Product Details
        </div>
        <div
          style={{
            width: 50 * cof,
            height: 50 * cof,
            borderRadius: 10,
            boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.10) inset",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
          onClick={shareLink}
        >
          <img
            alt="shareMenu"
            src={shareMenu}
            style={{ width: 34 * cof, height: 34 * cof }}
          />
        </div>
      </div>
    </div>
  );
};

export default HeaderProduct;
