import React from "react";
import ReactStars from "react-rating-stars-component";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import {
  changeBack,
  changeRating,
  changeReview,
  setActiveStep1,
  setActiveStep2,
  setIndexText,
  setNextText,
  setPrevText,
  setSelectTExt,
} from "../../redux/appReducer";
import back from "../../assets/icons/backStep2.png";
import more from "../../assets/icons/moreStep2.png";
import next from "../../assets/icons/next.png";
import { texts } from "../../utils/mockTexts";

const BtnCont = ({ cof, step, userText }) => {
  const {
    doneStep1,
    slider1,
    slider2,
    slider3,
    slider4,
    slider5,
    slider6,
    doneStep2,
    indexText,
    prevText,
    nextText,
  } = useSelector((state) => state.appReducer);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const toStep3 = () => {
    if (doneStep1) {
      dispatch(setPrevText({ index: indexText, step: step }));
      dispatch(setActiveStep1(false));
      if (userText) {
        dispatch(setSelectTExt(userText));
      }
      navigate("/review_step3");
    }
  };
  const prev = () => {
    dispatch(setActiveStep2(false));
    if (prevText) {
      setTimeout(() => timeOut(), 100);
    } else {
      setTimeout(() => timeOutNext(), 100);
    }
  };
  const timeOut = () => {
    dispatch(setIndexText(prevText.index));
    dispatch(setActiveStep2(true));
    dispatch(setPrevText(false));
    dispatch(setNextText(indexText));
    dispatch(setSelectTExt(false));
  };
  const timeOutNext = () => {
    dispatch(setIndexText(nextText));
    dispatch(setPrevText({ index: indexText }));
    dispatch(setActiveStep2(true));
    dispatch(setNextText(false));
    dispatch(setSelectTExt(false));
  };
  const copy = () => {
    navigator.clipboard
      .writeText(texts[indexText].text)
      .then(() => {
        console.log("copy");
      })
      .catch((err) => console.log(" : ", err));
  };
  const rate = () => {
    copy();
    if (slider6[0] === 0 && slider4[0] === 0 && slider5[0] === 0) {
      if (
        Number(((slider1[0] + slider2[0] + slider3[0]) / 20 / 3).toFixed(0)) < 4
      ) {
        navigate("/alt_review");
      } else {
        dispatch(
          changeRating(
            Number(((slider1[0] + slider2[0] + slider3[0]) / 20 / 3).toFixed(0))
          )
        );
        if (userText) {
          dispatch(setSelectTExt(userText));
        }
        dispatch(changeReview(texts[indexText].text));
        dispatch(changeBack("/review_step2"));
        navigate("/done");
      }
    } else {
      if (
        Number(
          (
            (slider1[0] +
              slider2[0] +
              slider3[0] +
              slider4[0] +
              slider5[0] +
              slider6[0]) /
            20 /
            6
          ).toFixed(0)
        ) < 4
      ) {
        navigate("/alt_review");
      } else {
        dispatch(
          changeRating(
            Number(
              (
                (slider1[0] +
                  slider2[0] +
                  slider3[0] +
                  slider4[0] +
                  slider5[0] +
                  slider6[0]) /
                20 /
                6
              ).toFixed(0)
            )
          )
        );
        if (userText) {
          dispatch(setSelectTExt(userText));
        }
        dispatch(changeReview(texts[indexText].text));
        dispatch(changeBack("/review_step3"));
        navigate("/done");
      }
    }
  };
  return (
    <div
      style={{
        position: "absolute",
        top: 84 * cof,
        zIndex: 999,
        display: "flex",
        left: 27.5 * cof,
        width: 338 * cof,
        justifyContent: "space-between",
      }}
    >
      <div
        style={{
          width: 92 * cof,
          height: 35 * cof,
          boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.25)",
          borderRadius: 72 * cof,
          background: "#D7B994",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          position: "relative",
        }}
      >
        {doneStep1 && !doneStep2 && (
          <div
            style={{
              position: "absolute",
              top: -15 * cof,
              color: "#fff",
              fontSize: 17 * cof,
              fontWeight: 700,
            }}
          >
            {Number(
              ((slider1[0] + slider2[0] + slider3[0]) / 20 / 3).toFixed(0)
            )}
          </div>
        )}
        {doneStep2 && (
          <div
            style={{
              position: "absolute",
              top: -15 * cof,
              color: "#fff",
              fontSize: 17 * cof,
              fontWeight: 700,
            }}
          >
            {Number(
              (
                (slider1[0] +
                  slider2[0] +
                  slider3[0] +
                  slider4[0] +
                  slider5[0] +
                  slider6[0]) /
                20 /
                6
              ).toFixed(0)
            )}
          </div>
        )}
        {doneStep1 && !doneStep2 && (
          <ReactStars
            count={5}
            //   onChange={ratingChanged}
            size={14}
            activeColor="#F0F419"
            value={Number(
              ((slider1[0] + slider2[0] + slider3[0]) / 20 / 3).toFixed(0)
            )}
          />
        )}

        {doneStep2 && (
          <ReactStars
            count={5}
            //   onChange={ratingChanged}
            size={14}
            activeColor="#F0F419"
            value={Number(
              (
                (slider1[0] +
                  slider2[0] +
                  slider3[0] +
                  slider4[0] +
                  slider5[0] +
                  slider6[0]) /
                20 /
                6
              ).toFixed(0)
            )}
          />
        )}
        {!doneStep1 && (
          <ReactStars
            count={5}
            //   onChange={ratingChanged}
            size={14}
            activeColor="#F0F419"
            value={0}
          />
        )}
      </div>
      <div
        style={{
          width: 92 * cof,
          height: 35 * cof,
          boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.25)",
          borderRadius: 72 * cof,
          background: "#fff",
          color: doneStep1 ? "#33302D" : "#C7C7C7",
          fontSize: 14 * cof,
          fontWeight: 700,
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
        onClick={rate}
      >
        Rate Now
      </div>
      {doneStep2 ? (
        <div style={{ display: "flex", alignItems: "center" }}>
          <div
            style={{
              width: 62 * cof,
              height: 35 * cof,
              borderRadius: "72px 0px 0px 72px",
              background: "#fff",
              boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.25)",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
            onClick={prev}
          >
            <img
              src={prevText ? back : next}
              alt="back"
              style={{ width: 20 * cof }}
            />
          </div>
          <div
            style={{
              width: 28 * cof,
              height: 35 * cof,
              borderRadius: "0px 72px 72px 0px",
              boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.25)",
              background: "#fff",
              marginLeft: 2 * cof,
              display: "flex",

              alignItems: "center",
            }}
          >
            <img
              src={more}
              alt="more"
              style={{ width: 5 * cof, marginLeft: 9 * cof }}
            />
          </div>
        </div>
      ) : (
        <div
          style={{
            width: 92 * cof,
            height: 35 * cof,
            boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.25)",
            borderRadius: 72 * cof,
            background: "#fff",
            color: doneStep1 ? "#33302D" : "#C7C7C7",
            fontSize: 14 * cof,
            fontWeight: 700,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
          onClick={toStep3}
        >
          Continue
        </div>
      )}
    </div>
  );
};

export default BtnCont;
