import React from "react";
import { Slider, Rail, Handles, Tracks } from "react-compound-slider";
import fire from "../../assets/icons/fire.svg";

const Handle = ({
  //     
  handle: { id, value, percent, cof },
  getHandleProps,
  top,
  left,
}) => (
  <div
    style={{
      left: `${percent}%`,
      position: "absolute",
      marginLeft: -20,
      top: -25,
      zIndex: 2,
      width: 45 * cof,
      height: 45 * cof,
      border: 0,
      textAlign: "center",
      cursor: "pointer",
      borderRadius: "50%",
      // border: "1px solid red",
      //   backgroundColor: "#2C4870",
      //  URL   
    }}
    {...getHandleProps(id)}
  >
    <img src={fire} style={{ width: 30 * cof, height: 30 * cof }} />
  </div>
);
const Track = ({ source, target, getTrackProps, top, left }) => (
  <div
    style={{
      //   marginTop: top,
      //   marginLeft: left,
      position: "absolute",
      height: 4,
      zIndex: 1,
      backgroundColor: "#FF7676",
      borderRadius: 3,
      cursor: "pointer",
      left: `${source.percent}%`,
      width: `${target.percent - source.percent}%`,
    }}
    {...getTrackProps()}
  />
);

function RangeSlider({ cof, top, left, values, setValues }) {
  //   const [values, setValues] = React.useState([50]);

  return (
    <Slider
      rootStyle={{
        position: "relative",
        width: 100 * cof,
        touchAction: "none",
      }}
      domain={[0, 100]}
      step={1}
      mode={1}
      values={values}
      onSlideEnd={setValues}
    >
      <Rail>
        {({ getRailProps }) => (
          <div
            style={{
              position: "absolute",
              width: 100 * cof,
              height: 3,
              borderRadius: 4,
              backgroundColor: "#FFEFD1",
              //   marginTop: top,
              //   marginLeft: left,
            }}
            {...getRailProps()}
          />
        )}
      </Rail>
      <Handles>
        {({ handles, getHandleProps }) => (
          <div>
            {handles.map((handle) => (
              <Handle
                key={handle.id}
                handle={handle}
                getHandleProps={getHandleProps}
                top={top}
                left={left}
                cof={cof}
              />
            ))}
          </div>
        )}
      </Handles>
      <Tracks left={true} right={false}>
        {({ tracks, getTrackProps }) => (
          <div>
            {tracks.map(({ id, source, target }) => (
              <Track
                key={id}
                source={source}
                target={target}
                getTrackProps={getTrackProps}
                top={top}
                left={left}
              />
            ))}
          </div>
        )}
      </Tracks>
    </Slider>
  );
}

export default RangeSlider;
