import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";

const HeaderShowItem = ({ cof }) => {
  const [showPopup, setShowPopup] = useState(false);

  useEffect(() => {
    setTimeout(() => setShowPopup(true), 1500);
    setTimeout(() => setShowPopup(false), 6500);
  }, [setShowPopup]);

  return (
    <div
      style={{
        width: 359 * cof,
        marginLeft: 17 * cof,
        paddingTop: 29 * cof,
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        position: "relative",
      }}
    >
      <div
        style={{
          width: 50 * cof,
          height: 50 * cof,
          boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.10) inset",
          borderRadius: 10,
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <img
          src={require("../../assets/icons/qrHeader.png")}
          style={{ width: 32 * cof, height: 32 * cof }}
        />
      </div>
      <div style={{ fontSize: 12 * cof, fontWeight: "900", color: "#000" }}>
        Online Menu
      </div>
      <Link
        style={{
          width: 107 * cof,
          height: 50 * cof,
          boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.10) inset",
          borderRadius: 10,
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          color: "#888687",
          fontSize: 12 * cof,
          fontWeight: 400,
        }}
        to={"/menu_order"}
      >
        My order
      </Link>
      {showPopup && (
        <div
          style={{
            position: "absolute",
            right: -11 * cof,
            background: "#fff",
            top: 83 * cof,
            zIndex: 99999,
          }}
        >
          <img
            src={require("../../assets/icons/popupLeftCont.png")}
            style={{ width: 243 * cof, height: 93 * cof }}
          />
          <div
            style={{
              position: "absolute",
              top: 42 * cof,
              left: 19 * cof,
              display: "flex",
              alignItems: "center",
            }}
          >
            <img
              src={require("../../assets/icons/closePopup.png")}
              style={{ width: 14 * cof, height: 14 * cof }}
              onClick={() => setShowPopup(false)}
            />
            <div
              style={{
                width: 160 * cof,
                fontSize: 12 * cof,
                fontWeight: "400",
                marginLeft: 29 * cof,
                color: "#fff",
              }}
            >
              You can view your complete order here.
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default HeaderShowItem;
