import React, { useEffect, useState } from "react";
import SliderSocialCont from "./SliderSocialCont";
import AlbumSlider2 from "./AlbumSlider2";
import AlbumSlider3 from "./AlbumSlider3";
import SliderS from "../../utils/Slider";
import Header from "../Header/Header";
import BottomBar from "../BottomBar/BottomBar";
import SocialContIcons from "./SocialContIcons";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import InstructionPopup from "../InstructionPopup/InstructionPopup";
import AlbumSlider4 from "./AlbumSlider4";
import AlbumSlider5 from "./AlbumSlider5";

const AlbumPage = ({ windowWidth }) => {
  const { pwa } = useSelector((state) => state.appReducer);
  const [popupInstructions, setPopupInstructions] = useState(false);
  const [clickEv, setClickEv] = useState(false);
  const cof = windowWidth / 393;
  useEffect(() => {
    const first = localStorage.getItem("first");
    if (pwa == false) {
      if (!first) {
        setClickEv(true);
      }
    }
  }, [setClickEv]);
  const [imgList, setImgList] = useState([
    {
      img: require("../../assets/icons/img1.png"),
      active: true,
      link: "https://firebasestorage.googleapis.com/v0/b/pixomniapromo.appspot.com/o/img3.png?alt=media&token=80372d52-a172-4c2c-84d5-cb2e3b3d8aa2",
    },
    {
      img: require("../../assets/icons/img2.png"),
      active: false,

      link: "https://firebasestorage.googleapis.com/v0/b/pixomniapromo.appspot.com/o/img1.png?alt=media&token=90e6b4dc-75b9-4bb4-a7d7-49b991861c28",
    },
    {
      img: require("../../assets/icons/img3.png"),
      active: false,
      link: "https://firebasestorage.googleapis.com/v0/b/pixomniapromo.appspot.com/o/img2.png?alt=media&token=d472316c-e483-40f3-96dc-3c2084205710",
    },
  ]);
  const [showSocial, setShowSocial] = useState(false);
  const components = [
    <div
      style={{
        width: pwa ? 377 * cof : 362 * cof,
        marginLeft: pwa ? 8 * cof : 15.5 * cof,
        position: "relative",
        marginTop: 10 * cof,
      }}
      onClick={() => setShowSocial(true)}
    >
      <SliderSocialCont cof={cof} />
    </div>,
    <Link to="/templates">
      <div
        style={{
          width: pwa ? 377 * cof : 362 * cof,
          marginLeft: pwa ? 8 * cof : 15.5 * cof,
          marginTop: 10 * cof,
          // height: 133 * cof,
          position: "relative",
        }}
      >
        <AlbumSlider4 cof={cof} />
      </div>
    </Link>,
    <Link to="/review">
      <div
        style={{
          width: pwa ? 377 * cof : 362 * cof,
          marginLeft: pwa ? 8 * cof : 15.5 * cof,
          marginTop: 10 * cof,
          // height: 133 * cof,
          position: "relative",
        }}
      >
        <AlbumSlider5 cof={cof} />
      </div>
    </Link>,
    <Link to="/t_shirt?category=mugs">
      <div
        style={{
          width: pwa ? 377 * cof : 362 * cof,
          marginLeft: pwa ? 8 * cof : 15.5 * cof,
          marginTop: 10 * cof,
          position: "relative",
        }}
      >
        <AlbumSlider2 cof={cof} />
      </div>
    </Link>,
    <Link to="t_shirt?category=t_shirts">
      <div
        style={{
          width: pwa ? 377 * cof : 362 * cof,
          marginLeft: pwa ? 8 * cof : 15.5 * cof,
          marginTop: 10 * cof,
          // height: 133 * cof,
          position: "relative",
        }}
      >
        <AlbumSlider3 cof={cof} />
      </div>
    </Link>,
  ];
  const getActive = () => {
    const filter = imgList.filter((el) => el.active);
    return filter[0].img;
  };
  const changeActive = (index) => {
    const newList = imgList.map((item, i) => {
      if (index === i) {
        return { ...item, active: true };
      } else {
        return { ...item, active: false };
      }
    });
    setImgList(newList);
  };
  const changeClickEv = () => {
    setClickEv(false);
    setPopupInstructions(true);
  };
  return (
    <>
      {clickEv && !pwa && (
        <div
          style={{
            position: "absolute",
            zIndex: 9999,
            width: "100%",
            height: "100vh",
            background: "transparent",
          }}
          onClick={changeClickEv}
        ></div>
      )}

      <Header windowWidth={windowWidth} />
      {popupInstructions && (
        <InstructionPopup
          cof={cof}
          setPopup={setPopupInstructions}
          text={
            "Enhance your experience with Tiki's app  on your home screen for exclusive deals\nand access to stunning Instagram Reels\nand TikTok templates, all for free!"
          }
        />
      )}

      <div style={{ marginTop: 12 * cof }}>
        <div
          style={{ width: "100%", display: "flex", justifyContent: "center" }}
        >
          <Link to="/buy_img">
            <img src={getActive()} style={{ height: 241 * cof }} alt="img" />
          </Link>
        </div>
        <div
          style={{ display: "flex", marginTop: 3 * cof, marginLeft: 12 * cof }}
        >
          {imgList.map((item, key) => (
            <div onClick={() => changeActive(key)} key={key}>
              <img
                src={item.img}
                alt="img"
                style={{
                  height: pwa ? 86 * cof : 62 * cof,
                  marginLeft: 3 * cof,
                }}
              />
            </div>
          ))}
        </div>
        {!showSocial && <SliderS components={components} cof={cof} />}
        {!pwa && showSocial ? (
          <div></div>
        ) : (
          <BottomBar windowWidth={windowWidth} indexType={0} />
        )}

        {showSocial && (
          <SocialContIcons cof={cof} setShowSocial={setShowSocial} />
        )}
      </div>
    </>
  );
};

export default AlbumPage;
