import React, { useCallback, useEffect, useState } from "react";
import del from "../../assets/icons/del.png";
import print from "../../assets/icons/print.png";
import share from "../../assets/icons/share.png";
import Cart from "./Cart";
import confirm from "../../assets/icons/confirm.png";
import "../../App.css";
import Archive from "./Archive";
import BottomBar from "../BottomBar/BottomBar";
import more from "../../assets/icons/more.png";
import template from "../../assets/icons/tempAlbum.svg";
import { Link, useNavigate } from "react-router-dom";
import InstructionPopup from "../InstructionPopup/InstructionPopup";
import { useSelector } from "react-redux";
import ins from "../../assets/icons/instAlb.svg";
import tikTok from "../../assets/icons/tiktokAlbum.svg";
import fullScreen from "../../assets/icons/fullScreen.svg";
import closeFull from "../../assets/icons/closeFull.svg";
import videoImg from "../../assets/icons/videoImg.svg";
import vid from "../../assets/icons/promovid2.mp4";

const PageBuyImg = ({ windowWidth }) => {
  const cof = windowWidth / 393;
  const navigate = useNavigate();
  const { pwa } = useSelector((state) => state.appReducer);
  const [popupInstructions, setPopupInstructions] = useState(false);
  const [showFussScreen, setShowFullScreen] = useState(false);
  const [showVideo, setShowVieo] = useState(false);
  const [imgList, setImgList] = useState([
    {
      img: require("../../assets/icons/img1.png"),
      active: false,
      id: 0,
      backGroundRemove: false,
      type: "photo",
      addCart: false,
      buy: true,
      link: "https://firebasestorage.googleapis.com/v0/b/pixomniapromo.appspot.com/o/img3.png?alt=media&token=80372d52-a172-4c2c-84d5-cb2e3b3d8aa2",
    },
    {
      img: require("../../assets/icons/img3.png"),
      active: false,
      id: 1,
      backGroundRemove: false,
      type: "photo",
      addCart: false,
      buy: true,
      link: "https://firebasestorage.googleapis.com/v0/b/pixomniapromo.appspot.com/o/img1.png?alt=media&token=90e6b4dc-75b9-4bb4-a7d7-49b991861c28",
    },
    {
      img: require("../../assets/icons/img2.png"),
      active: false,
      id: 2,
      backGroundRemove: false,
      type: "photo",
      addCart: false,
      buy: true,
      link: "https://firebasestorage.googleapis.com/v0/b/pixomniapromo.appspot.com/o/img2.png?alt=media&token=d472316c-e483-40f3-96dc-3c2084205710",
    },
    {
      img: require("../../assets/icons/prew.png"),
      active: false,
      id: 3,
      backGroundRemove: false,
      type: "photo",
      addCart: false,
      buy: true,
      video: true,
      videoLink: require("../../assets/icons/promovid2.mp4"),
      play: false,
    },
  ]);
  const [cart, setCart] = useState([]);
  const [archive, setArchive] = useState([]);

  useEffect(() => {
    const cartStorage = localStorage.getItem("cartData");
    const imgListStorage = localStorage.getItem("imgListStorage");
    const archiveStorage = localStorage.getItem("archiveStorage");
    if (cartStorage) {
      setCart(JSON.parse(cartStorage));
    }
    if (imgListStorage) {
      const imgArray = JSON.parse(imgListStorage);
      imgArray.sort((a, b) => a.id - b.id);
      setImgList(imgArray);
    }
    if (archiveStorage) {
      setArchive(JSON.parse(archiveStorage));
    }
  }, [setCart, setImgList, setArchive]);
  const getIndex = (item) => {
    const filter = cart.filter((el) => el.type === "photo");
    console.log(filter);
    if (filter.length !== 0) {
      const index = filter.findIndex((el) => el.id === item.id);
      if (index === -1) {
        return false;
      } else {
        return true;
      }
    } else {
      return false;
    }
  };

  const addToCart = (item, index) => {
    let newArray = [...imgList];
    let newArrayCart = [...cart];
    newArrayCart = [
      ...newArrayCart,
      {
        ...item,
        title: `photo ${index + 1}`,
        count: 1,
        link: "",
        price: "20.00",
      },
    ];
    newArray[index].addCart = true;
    setCart(newArrayCart);
    setImgList(newArray);
    localStorage.setItem("cartData", JSON.stringify(newArrayCart));
    localStorage.setItem("imgListStorage", JSON.stringify(newArray));
  };
  const clickRemove = (item, index) => {
    let newArray = [...imgList];
    newArray[index].backGroundRemove = true;
    setImgList(newArray);
    setTimeout(() => removeToCart(item, index), 200);
  };
  const removeToCart = (item, index) => {
    let newArrayCart = [...cart];
    let newArrayImg = [...imgList];
    const indexRemoveItem = newArrayCart.findIndex((el) => el.id === item.id);
    newArrayCart.splice(indexRemoveItem, 1);
    newArrayImg[index].addCart = false;
    newArrayImg[index].backGroundRemove = false;
    setCart(newArrayCart);
    setImgList(newArrayImg);
    localStorage.setItem("cartData", JSON.stringify(newArrayCart));
    localStorage.setItem("imgListStorage", JSON.stringify(newArrayImg));
  };
  const selectAll = () => {
    setCart(imgList);
    const newArray = imgList.map((item) => {
      return { ...item, active: true };
    });
    setImgList(newArray);
    localStorage.setItem("cartData", JSON.stringify(imgList));
    localStorage.setItem("imgListStorage", JSON.stringify(newArray));
  };

  const addArchive = (item, index) => {
    let newArray = [...archive];
    let newArrayImg = [...imgList];
    newArray = [...newArray, item];
    newArrayImg[index].active = false;
    newArrayImg.splice(index, 1);
    setArchive(newArray);
    setImgList(newArrayImg);
    localStorage.setItem("archiveStorage", JSON.stringify(newArray));
    localStorage.setItem("imgListStorage", JSON.stringify(newArrayImg));
  };

  const revert = () => {
    let newImgList = [...imgList];
    let newArchiveList = [...archive];
    newImgList = [...newImgList, ...archive];
    newImgList.sort((a, b) => a.id - b.id);
    newArchiveList = [];
    setImgList(newImgList);
    setArchive(newArchiveList);
    localStorage.setItem("archiveStorage", JSON.stringify(newArchiveList));
    localStorage.setItem("imgListStorage", JSON.stringify(newImgList));
  };
  const toTemplate = (index) => {
    if (!pwa) {
      let newArray = [...imgList];
      newArray[index].active = false;
      console.log("polo");
      setImgList(newArray);
      setPopupInstructions(true);
    } else {
      navigate("/templates");
    }
  };
  const styles = {
    itemCont: {
      marginTop: 28 * cof,
      position: "relative",
      minHeight: 230 * cof,
    },
    activeCheckboxCont: { position: "absolute", left: 25 * cof },
    activeCheckBox: {
      width: 29 * cof,
      height: 29 * cof,
      borderRadius: 100,
      backgroundColor: "#BA8545",
      boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.25) inset",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    },
    activeCheckBoxImg: {
      width: "50%",
    },
    checkBoxText: {
      fontSize: 10 * cof,
      marginTop: 4 * cof,
    },
    checkBoxCont: {
      position: "absolute",
      left: 25 * cof,
    },
    checkBox: {
      width: 27 * cof,
      height: 27 * cof,
      borderRadius: 100,
      backgroundColor: "#E2E2E2",
      border: "1px solid #000",
      boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.25) inset",
    },
    btnCont: {
      display: "flex",
      position: "absolute",
      right: 36 * cof,
    },
    del: { display: "flex", flexDirection: "column", alignItems: "center" },
    print: {
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      marginLeft: 27 * cof,
    },
    share: {
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      marginLeft: 27 * cof,
    },
    btnImg: {
      height: 22 * cof,
    },
    btnText: { fontSize: 10 * cof, marginTop: 10 * cof },

    imageBtnCont: {
      width: 393 * cof,
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      position: "absolute",
      bottom: 26 * cof,
    },
    imageBtn: {
      width: 260 * cof,
      height: 48 * cof,
      borderRadius: 6 * cof,
      backgroundColor: "#BA8545",
      color: "#fff",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      fontWeight: "500",
      fontSize: 17 * cof,
    },
    imageInActiveBtn: {
      width: 260 * cof,
      height: 48 * cof,
      borderRadius: 6 * cof,
      backgroundColor: "#fff",
      color: "#BA8545",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      fontWeight: "500",
      fontSize: 17 * cof,
    },
    imageInActiveBtnRed: {
      width: 260 * cof,
      height: 48 * cof,
      borderRadius: 6 * cof,
      backgroundColor: "#B74A4A",
      color: "#fff",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      fontWeight: "500",
      fontSize: 17 * cof,
    },
  };

  const shareF = useCallback(async (item, index) => {
    let newArray = [...imgList];
    newArray[index].active = false;
    console.log("polo");
    setImgList(newArray);
    if (!pwa) {
      setPopupInstructions(true);
    } else {
      try {
        const response = await fetch(
          `https://stingray-app-lodnw.ondigitalocean.app/${item.link}`
        );
        const blob = await response.blob();
        const file = new File([blob], "share.jpg", { type: "image/jpeg" });

        await navigator.share({
          files: [file],
          // title: "Awesome Picture",
          // text: "Check out this awesome picture!",
        });

        console.log("Sharing was successful.");
      } catch (err) {
        console.log("Error during sharing", err);
      }
    }
  }, []);
  const changeActive = (index) => {
    let newArray = [...imgList];
    newArray[index].active = !newArray[index].active;
    setImgList(newArray);
  };
  const imgEvent = (item) => {
    console.log(item.video);
    if (item.video && !showVideo) {
      setShowVieo(item.videoLink);
    } else {
      setShowFullScreen(item);
    }
  };
  return (
    <div>
      {archive.length !== 0 && (
        <Archive cof={cof} data={archive} revertF={revert} cart={cart} />
      )}
      {popupInstructions && (
        <InstructionPopup
          cof={cof}
          setPopup={setPopupInstructions}
          text={
            "Exclusive to Home Screen.\nFollow instructions to add Tiki's\nAlbum in two clicks:"
          }
        />
      )}
      {showFussScreen && (
        <div
          style={{
            width: "100%",
            height: "100vh",
            position: "fixed",
            background: "#262626",
            zIndex: 9999,
            bottom: 0,
            overflow: "hidden",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <img
            src={closeFull}
            alt="close"
            style={{
              width: 24 * cof,
              position: "absolute",
              top: 10 * cof,
              left: 22 * cof,
              zIndex: 999,
            }}
            onClick={() => setShowFullScreen(false)}
          />

          <img
            style={{
              width: windowWidth > 500 ? "auto" : 393 * cof,
            }}
            src={showFussScreen.img}
            onClick={() => setShowFullScreen(false)}
          />
        </div>
      )}

      <div
        style={{
          marginTop: cart.length !== 0 && archive.length === 0 ? 0 : 0,
          paddingBottom: 91 * cof,
        }}
      >
        {imgList.map((item, key) => (
          <div key={key}>
            <div style={styles.itemCont}>
              {showVideo && (
                <div
                  style={{
                    background: "#262626",
                    width: "100%",
                    height: "100%",
                    position: "fixed",
                    zIndex: 999999,
                    top: 0,
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                  }}
                >
                  <div
                    style={{
                      position: "absolute",
                      top: 20 * cof,
                      left: 310 * cof,
                      zIndex: 9999999999999,
                    }}
                    onClick={() => setShowVieo(false)}
                  >
                    <img src={closeFull} alt="closeFull" style={{}} />
                  </div>

                  <video
                    autoPlay
                    controls
                    playsInline={true}
                    style={{ height: 550 * cof }}
                  >
                    <source src={item.videoLink} />
                  </video>
                </div>
              )}

              {item.video && (
                <img
                  alt="video"
                  src={videoImg}
                  style={{
                    width: 41 * cof,
                    height: 48 * cof,
                    position: "absolute",
                    zIndex: 999,
                    bottom: 20 * cof,
                    left: 19 * cof,
                  }}
                />
              )}
              {!item.video && (
                <img
                  src={fullScreen}
                  alt="fullScreen"
                  style={{
                    width: 24 * cof,
                    height: 24 * cof,
                    position: "absolute",
                    zIndex: 999,
                    top: 19,
                    left: 22,
                  }}
                  onClick={() => setShowFullScreen({ img: item.img })}
                />
              )}

              <img
                src={item.img}
                alt="img"
                style={{
                  width: 393 * cof,
                  height: item.video ? 226 * cof : "auto",
                  borderRadius: item.video ? 20 * cof : 0,
                  objectFit: "cover",
                }}
                onClick={() => imgEvent(item)}
              />
              {item.active && (
                <div
                  style={{
                    width: 189 * cof,
                    height: !item.video ? 164 * cof : 123 * cof,
                    borderRadius: 10,
                    background: "rgba(30, 30, 30, 0.75)",
                    position: "absolute",
                    bottom: 102 * cof,
                    right: 28 * cof,
                    zIndex: 99999,
                  }}
                >
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      marginLeft: 32 * cof,
                      height: 41 * cof,
                    }}
                  >
                    <img
                      atl="del"
                      src={del}
                      style={{ width: 15 * cof, height: 15 * cof }}
                    />
                    <div
                      style={{
                        color: "#fff",
                        fontSize: 13 * cof,
                        fontWeight: 500,
                        marginLeft: 26 * cof,
                      }}
                      onClick={() => addArchive(item, key)}
                    >
                      Delete
                    </div>
                  </div>
                  {!item.video && (
                    <div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        marginLeft: 32 * cof,
                        height: 41 * cof,
                      }}
                    >
                      <img
                        atl="print"
                        src={print}
                        style={{ width: 15 * cof, height: 15 * cof }}
                      />
                      <div
                        style={{
                          color: "#fff",
                          fontSize: 13 * cof,
                          fontWeight: 500,
                          marginLeft: 26 * cof,
                        }}
                      >
                        Print
                      </div>
                    </div>
                  )}

                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      marginLeft: 28 * cof,
                      height: 41 * cof,
                    }}
                    onClick={() => shareF(item, key)}
                  >
                    <img
                      atl="share"
                      src={share}
                      style={{ width: 24 * cof, height: 24 * cof }}
                    />
                    <div
                      style={{
                        color: "#fff",
                        fontSize: 13 * cof,
                        fontWeight: 500,
                        marginLeft: 21 * cof,
                      }}
                    >
                      Share
                    </div>
                  </div>
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      marginLeft: 5 * cof,
                      height: 41 * cof,
                    }}
                    onClick={() => toTemplate(key)}
                  >
                    <img
                      src={ins}
                      alt="insr"
                      style={{ width: 12 * cof, height: 12 * cof }}
                    />
                    <img
                      src={tikTok}
                      alt="insr"
                      style={{
                        width: 11 * cof,
                        height: 11 * cof,
                        marginLeft: 2,
                      }}
                    />
                    <img
                      atl="template"
                      src={template}
                      style={{
                        width: 12 * cof,
                        height: 20 * cof,
                        marginLeft: 5,
                      }}
                    />
                    <div
                      style={{
                        color: "#fff",
                        fontSize: 13 * cof,
                        fontWeight: 500,
                        marginLeft: 26 * cof,
                      }}
                    >
                      Templates
                    </div>
                  </div>
                </div>
              )}

              <div
                style={{
                  background:
                    "linear-gradient(180deg, rgba(0, 0, 0, 0.00) 0%, rgba(0, 0, 0, 0.76) 81.77%)",
                  height: 128 * cof,
                  position: "absolute",
                  bottom: 0,
                  zIndex: 99,
                  width: "100%",
                  borderRadius: item.video ? 20 : 0,
                }}
              >
                {!item.video ? (
                  <div style={styles.imageBtnCont}>
                    {!item.addCart || !getIndex(item) ? (
                      <>
                        <div
                          style={styles.imageBtn}
                          onClick={() => addToCart(item, key)}
                        >
                          Add 1 to cart  $20.00
                        </div>
                        <div
                          style={{
                            width: 50 * cof,
                            height: 50 * cof,
                            borderRadius: 7 * cof,
                            background: "#BA8545",
                            boxShadow:
                              "0px 4px 4px 0px rgba(0, 0, 0, 0.25), 1px 1px 1px 0px rgba(0, 0, 0, 0.25) inset",
                            marginLeft: 15 * cof,
                            display: "flex",
                            justifyContent: "center",
                            alignItems: "center",
                          }}
                          onClick={() => changeActive(key)}
                        >
                          <img
                            alt="more"
                            src={more}
                            style={{ width: 37 * cof, height: 37 * cof }}
                          />
                        </div>
                      </>
                    ) : (
                      <>
                        <div
                          style={
                            !item.backGroundRemove
                              ? styles.imageInActiveBtn
                              : styles.imageInActiveBtnRed
                          }
                          onClick={() => clickRemove(item, key)}
                        >
                          Remove from Cart  $20.00
                        </div>
                        <div
                          style={{
                            width: 50 * cof,
                            height: 50 * cof,
                            borderRadius: 7 * cof,
                            background: "#BA8545",
                            boxShadow:
                              "0px 4px 4px 0px rgba(0, 0, 0, 0.25), 1px 1px 1px 0px rgba(0, 0, 0, 0.25) inset",
                            marginLeft: 15 * cof,
                            display: "flex",
                            justifyContent: "center",
                            alignItems: "center",
                          }}
                          onClick={() => changeActive(key)}
                        >
                          <img
                            alt="more"
                            src={more}
                            style={{ width: 37 * cof, height: 37 * cof }}
                          />
                        </div>
                      </>
                    )}
                  </div>
                ) : (
                  <div
                    style={{
                      width: 50 * cof,
                      height: 50 * cof,
                      borderRadius: 7 * cof,
                      background: "#BA8545",
                      boxShadow:
                        "0px 4px 4px 0px rgba(0, 0, 0, 0.25), 1px 1px 1px 0px rgba(0, 0, 0, 0.25) inset",
                      marginLeft: 310 * cof,
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                      marginTop: 50 * cof,
                    }}
                    onClick={() => changeActive(key)}
                  >
                    <img
                      alt="more"
                      src={more}
                      style={{ width: 37 * cof, height: 37 * cof }}
                    />
                  </div>
                )}
              </div>
            </div>
            <div style={{ display: "flex", justifyContent: "center" }}>
              <div
                style={{
                  width: 160 * cof,
                  height: 1 * cof,
                  backgroundColor: "#BA8545",
                  marginTop: 20,
                }}
              ></div>
            </div>
          </div>
        ))}
        <div></div>
      </div>
      <div style={{ height: 50 }}></div>
      <BottomBar
        windowWidth={windowWidth}
        indexType={1}
        length={cart.length ? cart : undefined}
        album={true}
      />
    </div>
  );
};

export default PageBuyImg;
