import React, { useEffect, useState } from "react";
import HeaderCart from "../Header/HeaderCart";
import BottomBar from "../BottomBar/BottomBar";
import { Link, useSearchParams } from "react-router-dom";
import { mockData } from "../../utils/mockData";
import { useSelector } from "react-redux";

const Tshirt = ({ windowWidth }) => {
  const { pwa } = useSelector((state) => state.appReducer);
  let [searchParams, setSearchParams] = useSearchParams();
  const [data, setData] = useState([]);

  useEffect(() => {
    const category = searchParams.get("category");
    if (mockData[category]) {
      setData(mockData[category]);
    }
  }, [setData, setSearchParams]);
  const cof = windowWidth / 393;

  return (
    <div>
      <HeaderCart cof={cof} title={"T-shirts"} />

      <div
        style={{
          marginTop: 85 * cof,
          overflow: "scroll",
          height: pwa ? 600 * cof : 500 * cof,
        }}
      >
        <div
          style={{
            width: 359 * cof,
            marginLeft: 17 * cof,
            display: "flex",
            flexWrap: "wrap",
            justifyContent: "space-between",
          }}
        >
          {data.map((item, key) => (
            <Link
              style={{ marginTop: 10 * cof }}
              key={key}
              to={`/product?category=${searchParams.get(
                "category"
              )}&back=t_shirt&product_id=${item.id}`}
            >
              <img
                alt="img"
                src={item.img}
                style={{
                  width: 162 * cof,
                  height: 202 * cof,
                  objectFit: "cover",
                  borderRadius: 12,
                }}
              />
              <div
                style={{
                  fontSize: 10 * cof,
                  fontWeight: 600,
                  marginTop: 5 * cof,
                  color: "#000",
                }}
              >
                {item.title}
              </div>
              <div
                style={{
                  fontSize: 10 * cof,
                  fontWeight: 600,
                  color: "#000",
                }}
              >
                {item.type}
              </div>
              <div style={{ display: "flex", justifyContent: "space-between" }}>
                {/* <div
                  style={{
                    fontSize: 10 * cof,
                    fontWeight: 300,
                    color: "#000",
                  }}
                >
                  {item.id}
                </div> */}
                <div
                  style={{
                    fontSize: 10 * cof,
                    fontWeight: 600,
                    color: "#000",
                  }}
                >
                  ${item.price}
                </div>
              </div>
            </Link>
          ))}
        </div>
      </div>

      <BottomBar windowWidth={windowWidth} indexType={2} />
    </div>
  );
};

export default Tshirt;
