import React, { useEffect, useRef, useState } from "react";
import ReviewHeader from "./ReviewHeader";
import startBack from "../../assets/icons/backGroundDone.png";
import logoTiki from "../../assets/icons/done.png";
import plus from "../../assets/icons/plus.svg";
import checked from "../../assets/icons/checked.svg";
import ReactStars from "react-rating-stars-component";

import { useDispatch, useSelector } from "react-redux";
import {
  setActiveStep1,
  setActiveStep2,
  setDoneStep2,
  setIndexText,
  setSlider1,
  setSlider2,
  setSlider3,
  setSlider4,
  setSlider5,
  setSlider6,
} from "../../redux/appReducer";
import paperclip from "../../assets/icons/paperclip.png";

let slider_1 = [0];
let slider_2 = [0];
let slider_3 = [0];
const Done = ({ windowWidth }) => {
  const dispatch = useDispatch();

  const {
    indexText,
    doneStep1,
    activeStep1,
    slider1,
    slider2,
    slider3,
    reviewImages,
    review,
    rating,
    backReview,
    selectText,
  } = useSelector((state) => state.appReducer);
  const cof = windowWidth / 393;
  const fileInputRef = useRef(null);

  const [imagesPhone, setImagesPhone] = useState([]);
  const [selectedImages, setSelectedImages] = useState([]);
  const [albumImgList, setAlbumImg] = useState([]);
  useEffect(() => {
    const array = reviewImages.map((item) => {
      return { ...item, active: true };
    });
    setAlbumImg(array);
  }, [setAlbumImg]);
  const handleButtonClick = () => {
    //     ,      <input type="file" />
    fileInputRef.current.click();
  };

  const handleFileChange = (event) => {
    const files = event.target.files;
    //     Data URL      selectedImages
    for (let i = 0; i < files.length; i++) {
      const file = files[i];
      const reader = new FileReader();
      reader.onload = () => {
        setSelectedImages((prev) => [
          ...prev,
          { img: reader.result, active: true },
        ]);
      };
      reader.readAsDataURL(file);
    }
  };
  const unccheckedImages = (index) => {
    let newArray = [...selectedImages];
    newArray.splice(index, 1);
    setSelectedImages(newArray);
  };
  const changeActiveAlbum = (index) => {
    let newArray = [...albumImgList];
    newArray[index].active = !newArray[index].active;
    setAlbumImg(newArray);
  };
  const copy = () => {
    navigator.clipboard
      .writeText(selectText ? selectText : review)
      .then(() => {
        console.log("copy");
      })
      .catch((err) => console.log(" : ", err));
  };
  const toYelp = () => {
    copy();
    window.open(
      "https://www.yelp.com/writeareview/biz/rkDYptk3oCFzm38odi2ZCA?return_url=%2Fbiz%2FrkDYptk3oCFzm38odi2ZCA",
      "_blank"
    );
  };
  return (
    <div style={{ overflow: "hidden" }}>
      <ReviewHeader cof={cof} back={backReview} />
      <div style={{ top: 69 * cof, position: "absolute" }}>
        {/* <Messages /> */}

        <div
          style={{ position: "absolute", right: 20 * cof, top: 10 * cof }}
          onClick={toYelp}
        >
          <img
            src={logoTiki}
            alt="logo"
            style={{
              width: 104 * cof,
              // height: 174 * cof,
            }}
          />
        </div>
        <div
          style={{
            width: 393 * cof,
            position: "absolute",
            background: "#fff",
            top: 127 * cof,
            borderRadius: "25px 25px 0px 0px",
          }}
        >
          <div
            style={{
              display: "flex",
              alignItems: "center",
              marginTop: 28 * cof,
              marginLeft: 29 * cof,
            }}
          >
            <div
              style={{
                width: 92 * cof,
                height: 35 * cof,
                boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.25)",
                borderRadius: 72 * cof,
                background: "#D7B994",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                position: "relative",
              }}
            >
              <ReactStars
                count={5}
                //   onChange={ratingChanged}
                size={14}
                activeColor="#F0F419"
                value={rating}
              />
            </div>
            <div
              style={{
                fontSize: 23 * cof,
                color: "#4E453C",
                fontWeight: 700,
                marginLeft: 7 * cof,
              }}
            >
              {rating}
            </div>
          </div>
          <div
            style={{
              color: "#4E463C",
              fontWeight: 700,
              fontSize: 14 * cof,
              width: 367 * cof,
              marginLeft: 13 * cof,
              marginTop: 18 * cof,
            }}
          >
            {selectText ? selectText : review}
          </div>
          <div
            style={{
              width: 335 * cof,
              height: 1 * cof,
              background: "#ECCBA3",
              marginLeft: 19 * cof,
              marginTop: 15 * cof,
              position: "relative",
            }}
          >
            <div style={{ position: "absolute", right: -10, top: -15 }}>
              <img
                alt="paperClip"
                src={paperclip}
                style={{
                  width: 26 * cof,
                  height: 30 * cof,
                }}
              />
            </div>
          </div>
          <div
            style={{
              fontWeight: 800,
              fontSize: 16 * cof,
              marginTop: 12 * cof,
              marginLeft: 32 * cof,
            }}
          >
            My phone
          </div>
          <div style={{ marginTop: 15 * cof }}>
            <div
              style={{
                display: "flex",
                marginTop: 15 * cof,
                overflowX: "scroll",
                paddingRight: 10,
              }}
            >
              {selectedImages.map((item, key) => (
                <div
                  style={{ marginLeft: 10 * cof, position: "relative" }}
                  onClick={() => unccheckedImages(key)}
                  key={key}
                >
                  {item.active && (
                    <img
                      src={checked}
                      alt="checked"
                      style={{
                        width: 22 * cof,
                        height: 22 * cof,
                        position: "absolute",
                        right: 7 * cof,
                        top: 7 * cof,
                      }}
                    />
                  )}

                  <img
                    src={item.img}
                    style={{
                      width: 116 * cof,
                      height: 114 * cof,
                      borderRadius: 10 * cof,
                      objectFit: "cover",
                    }}
                  />
                </div>
              ))}
              <div>
                <input
                  type="file"
                  accept="image/*"
                  onChange={handleFileChange}
                  ref={fileInputRef}
                  style={{ display: "none" }}
                  multiple
                />
                <img
                  alt="plus"
                  src={plus}
                  style={{
                    width: 117 * cof,
                    height: 113 * cof,
                    marginLeft: 10,
                  }}
                  onClick={handleButtonClick}
                />
              </div>
            </div>
          </div>
          <div
            style={{
              fontWeight: 800,
              fontSize: 16 * cof,
              marginTop: 12 * cof,
              marginLeft: 32 * cof,
            }}
          >
            My phone
          </div>
          <div
            style={{
              display: "flex",
              marginTop: 15 * cof,
              overflowX: "scroll",
              paddingRight: 10,
            }}
          >
            {albumImgList.map((item, key) => (
              <div
                style={{ marginLeft: 10 * cof, position: "relative" }}
                onClick={() => changeActiveAlbum(key)}
                key={key}
              >
                {item.active && (
                  <img
                    src={checked}
                    alt="checked"
                    style={{
                      width: 22 * cof,
                      height: 22 * cof,
                      position: "absolute",
                      right: 7 * cof,
                      top: 7 * cof,
                    }}
                  />
                )}

                <img
                  src={item.img}
                  style={{
                    width: 116 * cof,
                    height: 114 * cof,
                    borderRadius: 10 * cof,
                    objectFit: "cover",
                  }}
                />
              </div>
            ))}
          </div>
        </div>
        <img src={startBack} style={{ width: 393 * cof }} />
      </div>
    </div>
  );
};

export default Done;
