import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";

const OrderHeader = ({ cof }) => {
  const [showPopup, setShowPopup] = useState(false);

  useEffect(() => {
    setTimeout(() => setShowPopup(true), 1500);
    setTimeout(() => setShowPopup(false), 6500);
  }, [setShowPopup]);

  return (
    <div
      style={{
        width: 359 * cof,
        marginLeft: 17 * cof,
        paddingTop: 29 * cof,
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        position: "relative",
      }}
    >
      <Link
        style={{
          width: 50 * cof,
          height: 50 * cof,
          boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.10) inset",
          borderRadius: 10,
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
        to={"/live_menu_show_item"}
      >
        <img
          src={require("../../assets/icons/backorder.png")}
          style={{ width: 32 * cof, height: 32 * cof }}
        />
      </Link>

      <div
        style={{
          width: 265 * cof,
          height: 50 * cof,
          boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.10) inset",
          borderRadius: 10,
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          color: "#888687",
          fontSize: 12 * cof,
          fontWeight: 400,
          background: "#E8E8E8",
        }}
      >
        My order
      </div>
      {showPopup && (
        <div
          style={{
            position: "absolute",
            left: -5 * cof,
            background: "#fff",
            top: 83 * cof,
            zIndex: 99999,
          }}
        >
          <img
            src={require("../../assets/icons/popupContRight.png")}
            style={{ width: 243 * cof, height: 93 * cof }}
          />
          <div
            style={{
              position: "absolute",
              top: 32 * cof,
              left: 19 * cof,
              display: "flex",
              alignItems: "center",
            }}
          >
            <div
              style={{
                width: 180 * cof,
                fontSize: 12 * cof,
                fontWeight: "400",

                color: "#fff",
              }}
            >
              Feel free to return to the Menu and add more items to your order
              here.
            </div>
            <img
              src={require("../../assets/icons/closePopup.png")}
              style={{
                width: 14 * cof,
                height: 14 * cof,
                marginLeft: 15 * cof,
              }}
              onClick={() => setShowPopup(false)}
            />
          </div>
        </div>
      )}
    </div>
  );
};

export default OrderHeader;
