import React, { useCallback, useEffect, useState } from "react";
import cartImg from "../../assets/icons/cartImg.png";
import minusCart from "../../assets/icons/minusCart.png";
import plusCart from "../../assets/icons/plusCart.png";
import share from "../../assets/icons/shareMenu.png";
import BottomBar from "../BottomBar/BottomBar";
import PromoCont from "./PromoCont";
import { Link, useNavigate, useSearchParams } from "react-router-dom";
import { click } from "@testing-library/user-event/dist/click";
import { useSelector } from "react-redux";
import trash from "../../assets/icons/trash.svg";
import cartClear from "../../assets/icons/cartClear.svg";
const Cart = ({ windowWidth }) => {
  const { pwa } = useSelector((state) => state.appReducer);
  let [searchParams, setSearchParams] = useSearchParams();
  const cof = windowWidth / 393;
  const [showPromo, setShowPromo] = useState(false);
  const [data, setData] = useState([]);

  const shareF = useCallback(async (item) => {
    try {
      const response = await fetch(
        `https://stingray-app-lodnw.ondigitalocean.app/${item.img}`
      );
      const blob = await response.blob();
      const file = new File([blob], "share.jpg", { type: "image/jpeg" });

      await navigator.share({
        files: [file],
        // title: "Awesome Picture",
        // text: "Check out this awesome picture!",
      });

      console.log("Sharing was successful.");
    } catch (err) {
      console.log("Error during sharing", err);
    }
  }, []);

  const navigate = useNavigate();
  useEffect(() => {
    const cartData = localStorage.getItem("cartData");
    if (cartData) {
      setData(JSON.parse(cartData));
    }
    console.log(JSON.parse(cartData));
  }, [setData]);
  useEffect(() => {
    setTimeout(() => setShowPromo(true), 1000);
  }, [setShowPromo]);
  const shareLink = (url) => {
    console.log(url);
    if (navigator.share) {
      //   API share  
      navigator
        .share({
          url: url,
        })
        .then(() => console.log("  "))
        .catch((error) =>
          console.error("    :", error)
        );
    } else {
      console.log("API share     ");
    }
  };
  const toLink = (item, index) => {
    if (item.type === "photo") {
      let newArray = [...data];
      newArray[index].count = newArray[index].count + 1;
      setData(newArray);
      localStorage.setItem("cartData", JSON.stringify(newArray));
    } else {
      navigate(
        `/product?category=${item.category}&back=cart&product_id=${item.id}`
      );
    }
  };
  const shareitem = (item) => {
    if (item.type === "photo") {
      shareF(item);
    } else {
      shareLink(item.link);
    }
  };
  const minus = (index) => {
    let newArray = [...data];
    if (newArray[index].count !== 1) {
      newArray[index].count = newArray[index].count - 1;
    } else {
      newArray.splice(index, 1);
    }
    setData(newArray);
    localStorage.setItem("cartData", JSON.stringify(newArray));
  };
  return (
    <div style={{ background: "#FCFCFC" }}>
      {showPromo && <PromoCont cof={cof} setShowPromo={setShowPromo} />}
      {data.length === 0 ? (
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
          }}
        >
          <img
            alt="cart"
            src={cartClear}
            width={377 * cof}
            height={377 * cof}
          />
          <div style={{ fontSize: 20, fontWeight: 600, marginTop: 12 * cof }}>
            Add items to start a cart
          </div>
          <div
            style={{
              fontSize: 16,
              fontWeight: 600,
              marginTop: 12 * cof,
              color: "#151515",
              width: 250 * cof,
              textAlign: "center",
            }}
          >
            Once you add itams or store, your cart will appear here.
          </div>
        </div>
      ) : (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            height: "70vh",
            overflow: "scroll",
          }}
        >
          {data.map((item, key) => (
            <div
              style={{
                width: 360 * cof,
                height: 120 * cof,
                borderRadius: 15 * cof,
                backgroundColor: "EDEDED",
                boxShadow: "1px 1px 4px 1px rgba(0, 0, 0, 0.10) inset",
                display: "flex",
                marginTop: pwa ? 39 * cof : 13 * cof,
              }}
              key={key}
            >
              <img
                alt="img"
                src={item.img}
                style={{
                  width: 120 * cof,
                  height: 120 * cof,
                  borderRadius: 15 * cof,
                  objectFit: "cover",
                }}
              />
              <div>
                <div
                  style={{
                    display: "flex",
                    marginTop: 16 * cof,
                    marginLeft: 12 * cof,
                    width: 210 * cof,

                    justifyContent: "space-between",
                  }}
                >
                  <div>
                    <div
                      style={{
                        fontSize: 12 * cof,
                        fontWeight: 600,
                        color: "#000",
                      }}
                    >
                      {item.title}
                    </div>
                    {item.color && (
                      <div
                        style={{
                          fontSize: 12 * cof,
                          fontWeight: 500,
                          color: "#000",
                          marginTop: 5 * cof,
                        }}
                      >
                        {item.color}  Size {item.size}
                      </div>
                    )}

                    <div
                      style={{
                        fontSize: 12 * cof,
                        fontWeight: 400,
                        color: "#000",
                        marginTop: 5 * cof,
                      }}
                    >
                      {item.id}
                    </div>
                  </div>
                  <img
                    src={share}
                    alt="share"
                    style={{ width: 17 * cof, height: 17 * cof }}
                    onClick={() => shareitem(item)}
                  />
                </div>
                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    marginTop: 18 * cof,

                    marginLeft: 12 * cof,
                    width: 210 * cof,
                    justifyContent: "space-between",
                  }}
                >
                  <div style={{ fontSize: 12 * cof }}>${item.price}</div>
                  <div
                    style={{
                      width: 77 * cof,
                      height: 27 * cof,
                      boxShadow: "1px 1px 4px 1px rgba(0, 0, 0, 0.10) inset",
                      backgroundColor: "#fff",
                      borderRadius: 20,
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "space-around",
                    }}
                  >
                    <img
                      alt="minus"
                      src={item.count === 1 ? trash : minusCart}
                      style={{ width: 20 * cof, height: 20 * cof }}
                      onClick={() => minus(key)}
                    />
                    <div>{item.count}</div>
                    <img
                      alt="minus"
                      src={plusCart}
                      style={{ width: 20 * cof, height: 20 * cof }}
                      onClick={() => toLink(item, key)}
                    />
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}

      <div
        style={{
          width: "100%",
          display: "flex",
          justifyContent: "center",
          position: "absolute",
          bottom: pwa ? 146 * cof : 75 * cof,
        }}
      >
        {data.length === 0 ? (
          <Link
            to="/store"
            style={{
              width: 212 * cof,
              height: 40 * cof,
              borderRadius: 5,
              boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.03) inset",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              color: "#fff",
              fontSize: 16 * cof,
              fontWeight: 600,
              background: "#BA8545",
            }}
          >
            Start shopping
          </Link>
        ) : (
          <Link
            style={{
              width: 212 * cof,
              height: 40 * cof,
              borderRadius: 5,
              boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.03) inset",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              color: "#fff",
              fontSize: 16 * cof,
              fontWeight: 600,
              background: "#BA8545",
            }}
            to={"/pay_data"}
          >
            Checkout
          </Link>
        )}
      </div>
      {/* {data.length !== 0 && ( */}
      <BottomBar
        windowWidth={windowWidth}
        indexType={3}
        length={data.length ? data : undefined}
      />
      {/* )} */}
      {/* {data && <BottomBar windowWidth={windowWidth} indexType={2} />} */}
    </div>
  );
};

export default Cart;
