import React, { useState } from "react";
import confirm from "../../assets/icons/confirm.png";

const DeleveliDetails = ({ cof }) => {
  const [data, setData] = useState([
    { title: "Standard Delivery", active: true, type: "(2-3 days, $10.00)" },
    { title: "Nex Day Delivery", active: false, type: "( $25.00)" },
  ]);
  const onChangeData = (index) => {
    let newArray = data.map((item) => {
      return { ...item, active: false };
    });
    console.log(newArray);
    newArray[index].active = true;
    setData(newArray);
  };
  return (
    <div>
      {data.map((item, key) => (
        <div
          style={{
            width: 325 * cof,
            height: 77 * cof,
            borderRadius: 10,
            border:
              "1px solid var(--divider-light-1-px-60, rgba(228, 228, 228, 0.60))",
            backgroundColor: "#fff",
            display: "flex",
            paddingLeft: 13 * cof,
            marginTop: 7 * cof,
          }}
          onClick={() => onChangeData(key)}
          key={key}
        >
          <div
            style={{
              width: 24 * cof,
              height: 24 * cof,
              boxShadow: "0px 1px 0px 0px rgba(208, 201, 214, 0.40) inset",
              border: item.active ? "1px solid #fff" : "1px solid grey",
              borderRadius: 5 * cof,
              marginTop: 12 * cof,
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              backgroundColor: item.active ? "#BA8545" : "#fff",
            }}
          >
            <img
              alt="confirm"
              src={confirm}
              style={{ width: 15, height: 15 }}
            />
          </div>
          <div>
            <div
              style={{
                fontSize: 17 * cof,
                fontWeight: 600,
                marginTop: 13 * cof,
                marginLeft: 21 * cof,
              }}
            >
              {item.title}
            </div>
            <div
              style={{
                marginLeft: 21 * cof,
                color: "#999",
                marginTop: 5,
                fontWeight: 300,
              }}
            >
              {item.type}
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default DeleveliDetails;
