import React, { useEffect, useRef, useState } from "react";
import chat from "../../assets/icons/chat.png";

const Messages = ({ allItems, setAllItems, items, setItems, cof }) => {
  const scrollContainerRef = useRef(null);
  const [activeIndex, setActiveIndex] = useState(null);
  const scrollContainerHeight = "80px"; // Extracted for reuse

  useEffect(() => {
    //      sourceArray  destinationArray
    const moveData = () => {
      if (allItems.length > 0) {
        const updatedSourceArray = allItems.slice(1); //     sourceArray
        const firstElement = allItems[0]; //   
        setAllItems(updatedSourceArray);

        //     destinationArray
        setItems((prevDestinationArray) => [
          firstElement,
          ...prevDestinationArray,
        ]);
      }
    };

    const intervalId = setInterval(moveData, 5000); //    5 

    return () => {
      clearInterval(intervalId); //     
    };
  }, [allItems]);
  const handleScroll = () => {
    const { scrollTop, clientHeight } = scrollContainerRef.current;
    const scrollBottomPosition = scrollTop;

    let closestIndex = null;
    let closestDistance = Infinity;

    Array.from(scrollContainerRef.current.children).forEach((child, index) => {
      const childBottomPosition = child.offsetTop;
      const distance = Math.abs(scrollBottomPosition - childBottomPosition);

      if (distance < closestDistance) {
        closestDistance = distance;
        closestIndex = index;
      }
    });

    setActiveIndex(closestIndex);
  };

  useEffect(() => {
    const element = scrollContainerRef.current;
    element.addEventListener("scroll", handleScroll);
    handleScroll();

    return () => element.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <div
      style={{
        height: scrollContainerHeight,
        overflowY: "auto",
        transform: "rotateX(180deg)",
        position: "absolute",
        zIndex: 9999,
        // background: "#fff",
        width: "100%",
        top: 90 * cof,
      }}
      ref={scrollContainerRef}
    >
      {items.map((item, index) => (
        <div
          key={index}
          style={{
            padding: "10px",
            color: index === activeIndex ? "#946329" : "#946329",
            transform: "rotateX(180deg)",
            width: 330 * cof,
            fontSize: 12 * cof,
            fontWeight: 500,
            marginLeft: 34 * cof,
            opacity: index === activeIndex ? 1 : 0.3,
            display: "flex",
            flexDirection: "column",
            textShadow:
              "-1px -1px 0 #FFE2A9, 1px -1px 0 #FFE2A9, -1px 1px 0 #FFE2A9, 1px 1px 0 #FFE2A9",
          }}
        >
          <img
            src={chat}
            alt="chat"
            style={{ width: 13 * cof, height: 13 * cof }}
          />

          {item.item}
        </div>
      ))}
      <div style={{ height: scrollContainerHeight }}></div>{" "}
      {/* Bottom filler */}
    </div>
  );
};

export default Messages;
