import React, { useEffect, useState } from "react";
import HeaderProduct from "./HeaderProduct";
import mockImg from "../../assets/icons/mockImg.png";
import Constructor from "./Constructor";
import BottomBar from "../BottomBar/BottomBar";
import { useNavigate, useSearchParams } from "react-router-dom";
import { mockData } from "../../utils/mockData";
import Description from "./Description";
import { useSelector } from "react-redux";

const Product = ({ windowWidth }) => {
  const { pwa } = useSelector((state) => state.appReducer);
  const cof = windowWidth / 390;
  let [searchParams, setSearchParams] = useSearchParams();
  const navigate = useNavigate();
  const [colors, setColors] = useState([]);
  const [sizes, setSizes] = useState([
    {
      title: "S",
      active: false,
    },
    {
      title: "M",
      active: false,
    },
    {
      title: "L",
      active: true,
    },
    {
      title: "XL",
      active: false,
    },
    {
      title: "XXL",
      active: false,
    },
  ]);
  const [menu, setMenu] = useState([
    {
      title: "Description",
      active: true,
    },
    {
      title: "Color",
      active: false,
    },
    {
      title: "Reviews",
      active: false,
    },
  ]);
  const [data, setData] = useState(false);
  useEffect(() => {
    const product_id = searchParams.get("product_id");
    const category = searchParams.get("category");
    const categoryData = mockData[category];
    const productData = categoryData.filter((el) => el.id === product_id);
    setColors(productData[0].colors);
    setData(productData[0]);
  }, [setData, setColors]);
  const setSize = (index) => {
    let newArray = sizes.map((item) => {
      return { ...item, active: false };
    });
    newArray[index].active = true;
    setSizes(newArray);
  };
  const changeColor = (index) => {
    let newArray = colors.map((item) => {
      return { ...item, active: false };
    });
    newArray[index].active = true;
    setColors(newArray);
  };
  const changeMenu = (index) => {
    let newArray = menu.map((item) => {
      return { ...item, active: false };
    });
    newArray[index].active = true;
    setMenu(newArray);
  };

  function compareObjects(obj1) {
    //  ,      
    const { count, link, ...restObj1 } = obj1;

    //    
    return JSON.stringify(restObj1);
  }

  const addToCart = () => {
    const cartData = localStorage.getItem("cartData");
    let cartDataArray = JSON.parse(cartData);
    console.log(
      " ~ file: Product.js:109 ~ addToCart ~ cartDataArray:",
      cartDataArray
    );
    const category = searchParams.get("category");
    let dataToCart;
    if (category === "t_shirts") {
      const filterColor = colors.filter((el) => el.active);
      const filterSize = sizes.filter((el) => el.active);
      const color = filterColor[0].title;
      const size = filterSize[0].title;
      dataToCart = {
        ...data,
        size: size,
        color: color,
        category: category,
        link: window.location.href,
        count: 1,
      };
    } else {
      dataToCart = {
        ...data,
        category: category,
        link: window.location.href,
        count: 1,
      };
      console.log(dataToCart);
    }

    if (cartDataArray) {
      const filterObj = cartDataArray.filter(
        (el) => compareObjects(el) === compareObjects(dataToCart)
      );

      if (filterObj.length !== 0) {
        const index = cartDataArray.findIndex(
          (el) => compareObjects(el) === compareObjects(dataToCart)
        );
        cartDataArray[index].count = cartDataArray[index].count + 1;
      } else {
        cartDataArray = [...cartDataArray, dataToCart];
      }
    } else {
      cartDataArray = [dataToCart];
    }
    localStorage.setItem("cartData", JSON.stringify(cartDataArray));
    navigate("/cart");
  };

  console.log(data);
  return (
    <>
      {data && (
        <div style={{ background: "#FCFCFC" }}>
          <HeaderProduct cof={cof} />
          <div
            style={{
              position: "relative",
              width: 359 * cof,
              height: 193 * cof,
              marginLeft: 17 * cof,
              marginTop: 20 * cof,
            }}
          >
            <img
              alt="product"
              src={data?.img}
              style={{
                width: 359 * cof,
                height: 193 * cof,
                objectFit: "cover",
                borderRadius: 10,
              }}
            />
            <div
              style={{
                width: 65 * cof,
                height: 25 * cof,
                borderRadius: 10 * cof,
                backgroundColor: "#B6ED7F",
                boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.10) inset",
                fontSize: 12 * cof,
                fontWeight: 500,
                display: "flex",
                justifyContent: "center",
                color: "#fff",
                alignItems: "center",
                position: "absolute",

                left: 13 * cof,
                bottom: 11 * cof,
              }}
            >
              {data?.price}
            </div>
          </div>
          {searchParams.get("category") === "t_shirts" ? (
            <Constructor
              cof={cof}
              sizes={sizes}
              setSize={setSize}
              menu={menu}
              changeMenu={changeMenu}
              colors={colors}
              changeColor={changeColor}
              pwa={pwa}
            />
          ) : (
            <div style={{ marginTop: 20 * cof }}>
              <Description cof={cof} data={data} />
            </div>
          )}

          <div
            style={{
              width: "100%",
              display: "flex",
              justifyContent: "center",
              position: "absolute",
              bottom: pwa ? 146 * cof : 75 * cof,
            }}
          >
            <div
              style={{
                width: 212 * cof,
                height: 40 * cof,
                borderRadius: 5,
                boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.03) inset",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                color: "#fff",
                fontSize: 16 * cof,
                fontWeight: 600,
                background: "#BA8545",
              }}
              onClick={addToCart}
            >
              Add to cart
            </div>
          </div>

          <BottomBar windowWidth={windowWidth} indexType={2} />
        </div>
      )}
    </>
  );
};

export default Product;
