import React, { useEffect } from "react";
import PropTypes from "prop-types";
import ReviewHeader from "./ReviewHeader";
import startBack from "../../assets/icons/startBack.png";
import logoTiki from "../../assets/icons/tikiLogoReview.png";
import imgRview from "../../assets/icons/imgReview.png";
import waves from "../../assets/icons/waves.png";
import ScrollLoop from "../Store/scrollLoop";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  setActiveStep1,
  setDoneStep1,
  setSlider1,
  setSlider2,
  setSlider3,
} from "../../redux/appReducer";

const Review = ({ windowWidth }) => {
  const cof = windowWidth / 393;
  const { reviewImages } = useSelector((state) => state.appReducer);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(setSlider1([0]));
    dispatch(setSlider2([0]));
    dispatch(setSlider3([0]));
    dispatch(setActiveStep1(false));
    dispatch(setDoneStep1(false));
  }, [setSlider1, setSlider2, setSlider3, setActiveStep1, setDoneStep1]);
  return (
    <div style={{ overflow: "hidden" }}>
      <ReviewHeader cof={cof} />
      <div style={{ top: 69 * cof, position: "absolute" }}>
        <Link
          style={{
            width: 393 * cof,
            height: 700 * cof,
            background: "transparent",
            position: "absolute",
            zIndex: 9999,
          }}
          to={"/review_step2"}
        ></Link>
        <div
          style={{
            position: "absolute",
            top: 225 * cof,
            display: "flex",
            justifyContent: "center",
            width: 393 * cof,
            fontWeight: 800,
          }}
        >
          <div
            style={{
              fontSize: 14 * cof,
              color: "#FFF",
            }}
          >
            Tap to start
          </div>
        </div>

        <img
          src={logoTiki}
          alt="logo"
          style={{
            width: 124 * cof,
            // height: 174 * cof,
            position: "absolute",
            right: 4 * cof,
            top: -25 * cof,
          }}
        />
        <img
          src={imgRview}
          alt="imgReview"
          style={{
            position: "absolute",
            width: 393 * cof,
            top: 347 * cof,
          }}
        />
        <div style={{ position: "absolute", top: 445 * cof }}>
          <img
            src={waves}
            alt="waves"
            style={{ width: 393 * cof, position: "absolute" }}
          />
          <div
            style={{
              marginTop: 85 * cof,
              width: 393 * cof,
            }}
          >
            <ScrollLoop>
              {reviewImages.map((item, key) => (
                <img
                  src={item.img}
                  style={{ height: 133 * cof, margin: 5 * cof }}
                  key={key}
                />
              ))}
            </ScrollLoop>
          </div>
        </div>

        <img src={startBack} style={{ width: 393 * cof }} />
      </div>
    </div>
  );
};

export default Review;
