import React from "react";
import PropTypes from "prop-types";

const Color = ({ cof, color, changeColor }) => {
  return (
    <div
      style={{
        display: "flex",
        width: 90 * cof,
        justifyContent: "space-between",
        marginLeft: 17 * cof,
        marginTop: 37 * cof,
      }}
    >
      {color.map((item, key) => (
        <div
          key={key}
          style={{
            width: 24 * cof,
            height: 24 * cof,
            borderRadius: 100,
            border: `2px solid ${item.active ? "#BA8545" : "#D7DADD"}`,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
          onClick={() => changeColor(key)}
        >
          <div
            style={{
              width: 16 * cof,
              height: 16 * cof,
              backgroundColor: item.color,
              borderRadius: 100,
            }}
          ></div>
        </div>
      ))}
    </div>
  );
};

export default Color;
