import React from "react";
import back from "../../assets/icons/socialBannerSmall.png";
import backApp from "../../assets/icons/bannerApp.png";
import { useSelector } from "react-redux";

const SliderSocialCont = ({ cof }) => {
  const { pwa } = useSelector((state) => state.appReducer);
  const imgs = [
    { img: require("../../assets/icons/inst.png") },
    { img: require("../../assets/icons/fb.png") },
    { img: require("../../assets/icons/twitter.png") },
    { img: require("../../assets/icons/tube.png") },
    { img: require("../../assets/icons/yel.png") },
  ];
  return (
    <div style={{ display: "flex", justifyContent: "center" }}>
      <img
        src={pwa ? backApp : back}
        style={{ width: "100%", height: pwa ? 222 * cof : 140 * cof }}
        alt="img"
      />
    </div>
  );
};

export default SliderSocialCont;
