import React from "react";
import close from "../../assets/icons/close.png";
import { useDispatch, useSelector } from "react-redux";
import { setMenuShow, setSettings } from "../../redux/appReducer";
import { useNavigate } from "react-router-dom";
import logomenu from "../../assets/icons/logoBlack.png";

const Menu = ({ windowWidth }) => {
  const { pwa } = useSelector((state) => state.appReducer);
  const cof = windowWidth / 393;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const menu = [
    {
      title: "Album",
      link: "/album",
    },
    {
      title: "Store",
      link: "/store",
    },
    {
      title: "Menu",
      link: "/menu",
    },
    {
      title: "Tikis Club",
      link: "/club",
    },
    {
      title: "Reservations",
      link: "/reservations",
    },
    {
      title: "Delivery",
      link: "/deliver",
    },
    {
      title: "Contacts",
      link: "/deliver",
    },
    {
      title: "Settings",
      link: "/deliver",
    },
  ];
  const toLink = (item) => {
    if (item.title === "Settings") {
      dispatch(setMenuShow(false));
      dispatch(setSettings(true));
    } else {
      dispatch(setMenuShow(false));
      navigate(item.link);
    }
  };
  return (
    <div
      style={{
        width: "100%",
        height: "100vh",
        position: "absolute",
        zIndex: 999999999,
        backgroundColor: "#fff",
        top: 0,
      }}
    >
      <img
        style={{
          width: 14 * cof,
          height: 14 * cof,
          top: 51 * cof,
          marginLeft: 328 * cof,
          marginTop: 51 * cof,
        }}
        alt="close"
        src={close}
        onClick={() => dispatch(setMenuShow(false))}
      />
      <div style={{ marginLeft: 268 * cof, marginTop: 46 * cof }}>
        <img src={logomenu} alt="logomenu" width={77 * cof} height={50 * cof} />
      </div>
      <div style={{ marginRight: 45 * cof }}>
        {menu.map((item, key) => (
          <div
            key={key}
            style={{
              fontSize: 23 * cof,
              fontWeight: 300,
              color: "#000",
              textAlign: "end",
              marginTop: pwa ? 33 * cof : 25 * cof,
            }}
            onClick={() => toLink(item)}
          >
            {item.title}
          </div>
        ))}
      </div>
    </div>
  );
};

export default Menu;
