import React from "react";
import menu from "../../assets/icons/menu.svg";
import search from "../../assets/icons/search.png";
import { useDispatch, useSelector } from "react-redux";
import { setMenuShow } from "../../redux/appReducer";

const Header = ({ cof }) => {
  const dispatch = useDispatch();
  const { pwa } = useSelector((state) => state.appReducer);
  return (
    <div
      style={{
        width: 359 * cof,
        marginLeft: 17 * cof,
        height: 40 * cof,
        display: "flex",
        justifyContent: "space-between",
        paddingTop: pwa ? 39 * cof : 12 * cof,
      }}
    >
      <div
        style={{
          width: 272 * cof,
          height: 50 * cof,
          background: "#EDEDED",
          borderRadius: 10,
          boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.10) inset",
          display: "flex",
          alignItems: "center",
        }}
      >
        <input
          style={{
            position: "absolute",
            left: 70 * cof,
            height: 40,
            width: 200 * cof,
            background: "transparent",
            border: "none",
            outline: "none",
            fontSize: 16,
          }}
        />
        <img
          alt="search"
          src={search}
          style={{ width: 34 * cof, height: 34 * cof, marginLeft: 10 * cof }}
        />
      </div>
      <div
        style={{
          width: 50 * cof,
          height: 50 * cof,
          background: "#fff",
          borderRadius: 11 * cof,
          boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.10) inset",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
        onClick={() => dispatch(setMenuShow(true))}
      >
        <img
          alt="menu"
          src={menu}
          style={{ width: 34 * cof, height: 34 * cof }}
        />
      </div>
    </div>
  );
};

export default Header;
