import React, { useEffect, useState } from "react";
import right from "../../assets/icons/arrowRight.svg";
import hard from "../../assets/icons/hard.svg";
import archive from "../../assets/icons/archive.svg";
import img from "../../assets/icons/imgIco.svg";
import revert from "../../assets/icons/revert.png";
import video from "../../assets/icons/video.svg";
import { Link } from "react-router-dom";

const Archive = ({ windowWidth }) => {
  const cof = windowWidth / 393;
  const [data, setData] = useState([]);
  const [showDel, setShowDel] = useState(false);
  useEffect(() => {
    const archiveStorage = localStorage.getItem("archiveStorage");
    console.log(archiveStorage);
    if (archiveStorage) {
      const dateArray = JSON.parse(archiveStorage);
      const newDateArray = dateArray.map((item) => {
        return { ...item, activeDel: false };
      });
      setData(newDateArray);
    }
  }, [setData]);
  const del = (key) => {
    let newArray = [...data];
    newArray[key].activeDel = true;
    setData(newArray);
    setTimeout(() => deleteF(key), 1500);
  };
  const deleteF = (key) => {
    let newArray = [...data];
    newArray.splice(key, 1);
    setData(newArray);
    console.log(newArray);
    localStorage.setItem("archiveStorage", JSON.stringify(newArray));
  };
  const revertF = (item, key) => {
    const imgList = localStorage.getItem("imgListStorage");
    let newArrayImgList = JSON.parse(imgList);
    newArrayImgList = [...newArrayImgList, item];
    localStorage.setItem("imgListStorage", JSON.stringify(newArrayImgList));
    deleteF(key);
  };
  return (
    <div>
      <Link to={"/buy_img"}>
        <img
          src={right}
          style={{
            width: 27 * cof,
            paddingTop: 51 * cof,
            marginLeft: 351 * cof,
          }}
        />
      </Link>
      <div
        style={{
          width: 333 * cof,
          marginLeft: 30 * cof,
          display: "flex",
          justifyContent: "space-between",
          alignItems: "end",
          marginTop: 37 * cof,
        }}
      >
        <div style={{ display: "flex", alignItems: "center" }}>
          <img
            src={hard}
            alt="hard"
            style={{ width: 15 * cof, height: 15 * cof }}
          />
          <div style={{ fontSize: 12 * cof, marginLeft: 4 * cof }}>
            {data.length}
          </div>
        </div>
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <img src={archive} alt="archive" style={{ height: 19 * cof }} />
          <div style={{ fontSize: 12 * cof }}>Archive</div>
        </div>
      </div>
      <div
        style={{
          width: 333 * cof,
          height: 1,
          background: "#000000",
          marginLeft: 30 * cof,
          marginTop: 26 * cof,
        }}
      ></div>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          marginLeft: 30 * cof,
          marginTop: 10 * cof,
        }}
      >
        <img
          src={img}
          alt="img"
          style={{ width: 11 * cof, height: 11 * cof }}
        />
        <div style={{ fontSize: 12 * cof, marginLeft: 4 * cof }}>
          {data.length}
        </div>
      </div>
      <div style={{ marginTop: 10 * cof }}>
        {data.map((item, key) => (
          <div key={key} style={{ marginTop: 16 * cof }}>
            <div
              style={{ marginLeft: 323 * cof }}
              onClick={() => revertF(item, key)}
            >
              <img
                src={revert}
                alt="revert"
                style={{ width: 25 * cof, height: 25 * cof }}
              />
              <div style={{ fontSize: 7 * cof, fontWeight: 500 }}>Restore</div>
            </div>
            <div
              style={{
                width: 333 * cof,
                height: 118 * cof,
                background: item.activeDel ? "#FF5858" : "#fff",
                boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.25)",
                borderRadius: 15 * cof,
                marginLeft: 30 * cof,
                marginTop: 5,
                display: "flex",
              }}
            >
              <div
                style={{
                  width: "50%",
                  height: "100%",
                  display: "flex",
                  alignItems: "center",
                }}
                onClick={() => del(key)}
              >
                <div
                  style={{
                    fontSize: 24 * cof,
                    fontWeight: 500,
                    marginLeft: 45 * cof,
                    color: item.activeDel ? "#fff" : "#000",
                  }}
                >
                  Delete
                </div>
              </div>
              <div style={{ position: "relative" }}>
                <div
                  style={{
                    position: "absolute",
                    zIndex: 9999,
                    top: 48 * cof,
                    fontSize: 24 * cof,
                    fontWeight: 500,
                    color: "#DDD",
                    marginLeft: 45 * cof,
                  }}
                >
                  Archived
                </div>
                <img
                  src={item.img}
                  style={{
                    width: 170 * cof,
                    height: 118 * cof,
                    borderRadius: 10 * cof,
                    opacity: 0.7,
                  }}
                />
              </div>
            </div>
          </div>
        ))}
      </div>
      <div
        style={{
          width: 333 * cof,
          height: 1,
          background: "#000000",
          marginLeft: 30 * cof,
          marginTop: 26 * cof,
        }}
      ></div>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          marginLeft: 30 * cof,
          marginTop: 10 * cof,
        }}
      >
        <img src={video} alt="img" style={{ width: 14 * cof }} />
        <div style={{ fontSize: 12 * cof, marginLeft: 4 * cof }}>0</div>
      </div>
    </div>
  );
};

export default Archive;
