import React, { useEffect, useState } from "react";
import menu from "../../assets/icons/burgerNew.png";
import back from "../../assets/icons/back.png";
import { Link, useSearchParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { setMenuShow } from "../../redux/appReducer";

const HeaderCart = ({ cof, title }) => {
  const { pwa } = useSelector((state) => state.appReducer);
  const dispatch = useDispatch();
  let [searchParams, setSearchParams] = useSearchParams();
  const [menuData, setMenuData] = useState([
    { title: "T-Shirts", active: false, category: "t_shirts" },
    { title: "Mugs", active: false, category: "mugs" },
    { title: "Gift Cards", active: false, category: "gift_cards" },
  ]);
  useEffect(() => {
    const category = searchParams.get(`category`);
    const index = menuData.findIndex((el) => el.category === category);
    let newArray = menuData.map((item) => {
      return { ...item, active: false };
    });

    newArray[index].active = true;
    setMenuData(newArray);
  }, [setMenuData, setSearchParams]);
  const changeMenu = (item) => {
    setSearchParams(`category=${item.category}`);
    // const newArray = menuData.map((i) => {
    //   if (i.category === item.category) {
    //     return { ...item, active: true };
    //   } else {
    //     return { ...item, active: false };
    //   }
    // });
    // setMenuData(newArray);
  };
  return (
    <div
      style={{
        width: "100%",
        position: "fixed",
        backgroundColor: "#fff",
        top: 0,
        zIndex: 9999,
      }}
    >
      <div
        style={{
          marginTop: pwa ? 39 * cof : 30 * cof,
        }}
      >
        <div
          style={{
            display: "flex",
            width: 359 * cof,
            justifyContent: "space-between",
            marginLeft: 17 * cof,
          }}
        >
          <div style={{ display: "flex", marginLeft: -10 * cof }}>
            {menuData.map((item, key) => (
              <div
                key={key}
                style={{
                  width: 80 * cof,
                  height: 43 * cof,
                  borderRadius: 5 * cof,
                  boxShadow: "1px 4px 15px 0px rgba(0, 0, 0, 0.05)",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  fontSize: 10 * cof,
                  marginLeft: 10 * cof,
                  backgroundColor: item.active ? "#BA8545" : "#fff",
                  fontWeight: 600,
                }}
                onClick={() => changeMenu(item)}
              >
                {item.title}
              </div>
            ))}
          </div>
          <div
            style={{
              width: 50 * cof,
              height: 50 * cof,
              borderRadius: 10,
              boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.10) inset",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
            onClick={() => dispatch(setMenuShow(true))}
          >
            <img
              alt="menu"
              src={menu}
              style={{ width: 34 * cof, height: 34 * cof }}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default HeaderCart;
