import React, { useEffect, useState } from "react";
import HeaderOrder from "./HeaderOrder";
import orderImg from "../../assets/icons/order.png";
import Popup from "../Pay/Popup";
import PopupClub from "./PopupClub";

const Order = ({ windowWidth }) => {
  const cof = windowWidth / 393;
  const [popup, setPopup] = useState(false);
  useEffect(() => {
    setTimeout(() => setPopup(true), 3000);
  }, [setPopup]);
  return (
    <div>
      <HeaderOrder cof={cof} />
      {popup && <PopupClub cof={cof} setPopup={setPopup} />}

      <img
        alt="order"
        src={orderImg}
        style={{
          width: 325 * cof,
          height: 309 * cof,
          marginTop: 111 * cof,
          marginLeft: 34 * cof,
        }}
      />
    </div>
  );
};

export default Order;
