import React, { useEffect, useState } from "react";
import ReviewHeader from "./ReviewHeader";
import startBack from "../../assets/icons/backgroundStep2.png";
import logoTiki from "../../assets/icons/tikiLogoReview.png";
import waves from "../../assets/icons/waves.png";
import ScrollLoop from "../Store/scrollLoop";
import Range from "./Range";
import TextCont from "./TextCont";
import BtnCont from "./BtnCont";
import regenerate from "../../assets/icons/regenerate.png";
import logoRegenerate from "../../assets/icons/logoRegenerate.png";
import { useDispatch, useSelector } from "react-redux";
import {
  setActiveStep1,
  setActiveStep2,
  setDoneStep1,
  setDoneStep2,
  setIndexText,
  setSlider1,
  setSlider2,
  setSlider3,
  setSlider4,
  setSlider5,
  setSlider6,
} from "../../redux/appReducer";
import Messages from "./Messages";
import { useNavigate } from "react-router-dom";
let slider_1 = [0];
let slider_2 = [0];
let slider_3 = [0];
const ReviewStep2 = ({ windowWidth }) => {
  useEffect(() => {
    slider_1 = [0];
    slider_2 = [0];
    slider_3 = [0];
  }, []);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [allItems, setAllItems] = useState([
    {
      item: "Answer a few questions, and it  will generate your review effortlessly...",
    },
    { item: "Thank you for your positive feedback! ..." },
    {
      item: "If you're happy with the result, press 'Rate Now' to visit our Yelp page...",
    },
    {
      item: "Your review already copied for easy pasting, and you can choose the stars...",
    },
    {
      item: "Tap on the Tiki icon to generate a fresh review with your current settings...",
    },
  ]);
  const [items, setItems] = useState([
    { item: "Save time with our advanced, user-friendly Review Helper ..." },
  ]);
  useEffect(() => {
    console.log("ofjfiovgnrk");
    dispatch(setActiveStep2(false));
    dispatch(setDoneStep2(false));
    dispatch(setSlider4([0]));
    dispatch(setSlider5([0]));
    dispatch(setSlider6([0]));
  }, [setActiveStep2, setDoneStep2, setSlider4, setSlider5, setSlider6]);
  const {
    indexText,
    doneStep1,
    activeStep1,
    slider1,
    slider2,
    slider3,
    reviewImages,
  } = useSelector((state) => state.appReducer);
  const [userText, setUserText] = useState(false);
  const cof = windowWidth / 393;

  const timeout = () => {
    if (doneStep1) {
      dispatch(setActiveStep1(false));
      dispatch(setIndexText(indexText === 2 ? 0 : indexText + 1));
      dispatch(setDoneStep1(false));
      setUserText(false);
      setTimeout(() => changeIndex(), 100);
    }
  };
  const changeIndex = () => {
    dispatch(setActiveStep1(true));
  };
  const changeSlide1 = (e) => {
    slider_1 = e;
    dispatch(setSlider1(e));
    changeActive();
  };
  const changeSlide2 = (e) => {
    slider_2 = e;
    dispatch(setSlider2(e));
    changeActive();
  };
  const changeSlide3 = (e) => {
    slider_3 = e;
    dispatch(setSlider3(e));
    changeActive();
  };
  const changeActive = () => {
    if (slider_1[0] != 0 && slider_2[0] != 0 && slider_3[0] != 0) {
      if (
        Number(
          ((slider_1[0] + slider_2[0] + slider_3[0]) / 20 / 3).toFixed(0)
        ) < 4
      ) {
        navigate("/alt_review");
      } else {
        dispatch(setActiveStep1(true));
      }
    }
  };

  return (
    <div style={{ overflow: "hidden" }}>
      <ReviewHeader cof={cof} back={"/review"} />
      <div style={{ top: 69 * cof, position: "absolute" }}>
        <Messages
          allItems={allItems}
          setAllItems={setAllItems}
          items={items}
          setItems={setItems}
          cof={cof}
        />
        <Range
          cof={cof}
          slider1={slider1}
          slider2={slider2}
          slider3={slider3}
          setSlider1={changeSlide1}
          setSlider2={changeSlide2}
          setSlider3={changeSlide3}
          q1={"Rate the atmosphere and the view of our restaurant?"}
          q2={"How did you enjoy the food  during your visit?"}
          q3={"What did you think of the drinks?"}
        />
        <div
          style={{
            width: 335 * cof,
            height: 1,
            background: "#ECCBA3",
            position: "absolute",
            top: 174 * cof,
            left: 26.5 * cof,
          }}
        ></div>
        <div
          style={{ position: "absolute", right: 20 * cof, top: -20 * cof }}
          onClick={timeout}
        >
          <img
            src={logoTiki}
            alt="logo"
            style={{
              width: 104 * cof,
              // height: 174 * cof,
            }}
          />
          {doneStep1 && (
            <img
              src={regenerate}
              alt="regenerate"
              style={{
                width: 100 * cof,
                position: "absolute",
                left: 20 * cof,
                top: 80 * cof,
              }}
            />
          )}
          {doneStep1 && (
            <img
              src={logoRegenerate}
              alt="regenerate"
              style={{
                width: 42 * cof,
                position: "absolute",
                left: -30 * cof,
                top: 60 * cof,
              }}
            />
          )}
        </div>

        <TextCont
          cof={cof}
          active={activeStep1}
          indexText={indexText}
          step={"step1"}
          userText={userText}
          setUserText={setUserText}
        />
        <div style={{ position: "absolute", top: 445 * cof }}>
          <img
            src={waves}
            alt="waves"
            style={{ width: 393 * cof, position: "absolute" }}
          />
          <BtnCont cof={cof} step={"step1"} userText={userText} />
          <div
            style={{
              top: 146 * cof,
              width: 393 * cof,
              position: "absolute",
            }}
          >
            <ScrollLoop>
              {reviewImages.map((item, key) => (
                <img
                  src={item.img}
                  style={{ height: 133 * cof, margin: 5 * cof }}
                  key={key}
                />
              ))}
            </ScrollLoop>
          </div>
        </div>

        <img src={startBack} style={{ width: 393 * cof }} />
      </div>
    </div>
  );
};

export default ReviewStep2;
