const CHANGE_MENU = "CHANGE_MENU";
const CHANGE_PWA = "CHANGE_PWA";
const CHANGE_SETTINGS = "CHANGE_SETTINGS";
const INDEX_TEXT = "INDEX_TEXT";
const SET_DONE_STEP_1 = "SET_DONE_STEP_1";
const SET_ACTIVE_STEP_1 = "SET_ACTIVE_STEP_1";
const SET_SLIDER1 = "SET_SLIDER1";
const SET_SLIDER2 = "SET_SLIDER2";
const SET_SLIDER3 = "SET_SLIDER3";
const SET_SLIDER4 = "SET_SLIDER4";
const SET_SLIDER5 = "SET_SLIDER5";
const SET_SLIDER6 = "SET_SLIDER6";
const SET_ACTIVE_STEP_2 = "SET_ACTIVE_STEP_2";
const SET_DONE_STEP_2 = "SET_DONE_STEP_2";
const SET_PREV_TEXT = "PREV_TEXT";
const NEXT_TEXT = "NEXT_TEXT";
const SET_SELECT_TEXT = "SET_SELECT_TEXT";
const REVIEW_IMAGES = "REVIEW_IMAGES";
const CHANGE_RATING = "CHANGE_RATING";
const CHANGE_REVIEW = "CHANGE_REVIEW";
const CHANGE_BACK = "CHANGE_BACK";

const defaultState = {
  menu: false,
  pwa: false,
  settings: false,
  indexText: 0,
  doneStep1: false,
  activeStep1: false,
  slider1: [0],
  slider2: [0],
  slider3: [0],
  slider4: [0],
  slider5: [0],
  slider6: [0],
  activeStep2: false,
  doneStep2: false,
  prevText: false,
  nextText: false,
  selectText: false,
  rating: 0,
  review: "",
  reviewImages: [
    {
      img: require("../assets/icons/img1.png"),
    },
    {
      img: require("../assets/icons/img2.png"),
    },
    {
      img: require("../assets/icons/img3.png"),
    },
  ],
  backReview: "/",
};

export default function appReducer(state = defaultState, action) {
  switch (action.type) {
    case CHANGE_MENU:
      return {
        ...state,
        menu: action.payload,
      };
    case CHANGE_PWA:
      return {
        ...state,
        pwa: action.payload,
      };
    case CHANGE_SETTINGS:
      return {
        ...state,
        settings: action.payload,
      };
    case INDEX_TEXT:
      return {
        ...state,
        indexText: action.payload,
      };
    case SET_DONE_STEP_1:
      return {
        ...state,
        doneStep1: action.payload,
      };
    case SET_ACTIVE_STEP_1:
      return {
        ...state,
        activeStep1: action.payload,
      };
    case SET_SLIDER1:
      return {
        ...state,
        slider1: action.payload,
      };
    case SET_SLIDER2:
      return {
        ...state,
        slider2: action.payload,
      };
    case SET_SLIDER3:
      return {
        ...state,
        slider3: action.payload,
      };
    case SET_SLIDER4:
      return {
        ...state,
        slider4: action.payload,
      };
    case SET_SLIDER5:
      return {
        ...state,
        slider5: action.payload,
      };
    case SET_SLIDER6:
      return {
        ...state,
        slider6: action.payload,
      };
    case SET_ACTIVE_STEP_2:
      return {
        ...state,
        activeStep2: action.payload,
      };
    case SET_DONE_STEP_2:
      return {
        ...state,
        doneStep2: action.payload,
      };
    case SET_PREV_TEXT:
      return {
        ...state,
        prevText: action.payload,
      };
    case NEXT_TEXT:
      return {
        ...state,
        nextText: action.payload,
      };
    case SET_SELECT_TEXT:
      return {
        ...state,
        selectText: action.payload,
      };
    case REVIEW_IMAGES:
      return {
        ...state,
        reviewImages: action.payload,
      };
    case CHANGE_RATING:
      return {
        ...state,
        rating: action.payload,
      };
    case CHANGE_REVIEW:
      return {
        ...state,
        review: action.payload,
      };
    case CHANGE_BACK:
      return {
        ...state,
        backReview: action.payload,
      };
    default:
      return state;
  }
}
